/*Fonts*/
// @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,600&display=swap');

/*Common*/
*							{ margin: 0px; padding: 0px;}
html,body                   { height: 100%;}
body						{ font-family: 'Montserrat', sans-serif !important; font-size: 14px; line-height: 27px; background-color: $bg_color; color: $color_light_black;}
.clear						{ clear:both;}
img							{ border: 0px;}
ul,ol						{ margin: 0; list-style: none;}
a							{ display: inline-block; text-decoration: none; outline: none;}
a:focus, a:active,a:visited	{ outline: none;}
a:hover                     { text-decoration: none; color: $color_blue !important;}
.m-b-30                     { margin-bottom: 30px;}
.m-b-15                     { margin-bottom: 15px;} 
.m-15                       { margin: 15px 0;}
.m-0                        { margin: 0;}
.m-t-100                    { margin-top: 100px;}
b,strong                    { font-weight: $font_weight_700;}
button                      { border: none;}
button:focus                { outline: none;}
.btn:focus, .btn.focus      { box-shadow: none !important;}
.justify-space-between      { display: flex; display: -webkit-flex; align-items: center; -webkit-align-items: center; justify-content: space-between; -webkit-justify-content: space-between;}
.justify-space-around       { display: flex; display: -webkit-flex; justify-content: space-around; -webkit-justify-content: space-around;}
.align-center               { display: flex; display: -webkit-flex; align-items: center; -webkit-align-items: center;}
.align-end                  { display: flex; display: -webkit-flex; align-items: flex-end; -webkit-align-items: flex-end;}
.justify-end                { display: flex; display: -webkit-flex; justify-content: flex-end; -webkit-justify-content: flex-end;}
.justify-center             { display: flex; display: -webkit-flex; justify-content: center; -webkit-justify-content: center;}
.gray-color                 { color: $color_grey;}
.green-color                { color: #26A541 !important;}
.color-blue                 { color: $color_blue !important;}
.color-red                  { color: $color_red !important;}
.line-through               { text-decoration: line-through;}
.light-black                { color: $color_light_black;}
.btn-text                   { @include btn_text;}
.common-btn                 { @include common-btn;}
.common-btn.big             { @extend .common-btn; padding: 14px 40px;}
.common-btn.outline.big     { padding: 14px 40px;}
.common-btn.color-blue      { @include common_btn_color_blue; }
.btn-outline.color-blue     { padding: 8px 18px; border-color: $color_blue;}
.form-control               { @include form_control;}
textarea                    { resize: none; -webkit-resize: none; -moz-resize: none; -o-resize: none; -ms-resize: none; height: 150px !important;}
form label                  { font-size: 14px; line-height: 18px; color: #464546; font-weight: 500; text-transform: uppercase; letter-spacing: 1px;}
.form-group                 { margin-bottom: 20px;} 
.width-full                 { width: 100%;}
.section-padding            { padding: 50px 0;}
.section-padding-big        { padding: 100px 0;}
.grow-1                     { flex-grow: 1; -webkit-flex-grow: 1;}
.shrink-0                   { flex-shrink: 0; -webkit-flex-shrink: 0;}
.btn-1                      { @include btn_1;}
.btn-outline                { @include btn_outline;}
img                         { width: 100%;}
.common-title-size          { margin-bottom: 40px; font-size: 48px; line-height: 60px; text-align: center;}
/* Normal Dropdown */
.normal-dropdown {@include normal_dropdown; }

/* Responsive */
@media (min-width: 1200px) and (max-width: 1499px) { 
    .section-padding { padding: 40px 0;}
    .m-b-30          { margin-bottom: 20px;}
    // .form-group      { margin-bottom: 15px;}
}
@media (min-width: 992px) and (max-width: 1199px) { 
    .section-padding-big        { padding: 55px 0;}
}
@media (min-width: 768px) and (max-width: 991px) { 
    .m-t-100                    { margin-top: 130px;}
    .section-padding            { padding: 45px 0;}
    .section-padding-big        { padding: 55px 0;}
}
@media (max-width: 767px) { 
    .m-t-100                    { margin-top: 124px;}
    .section-padding            { padding: 35px 0;}
    .section-padding-big        { padding: 45px 0;}
    .form-group                 { margin-bottom: 12px;} 
    form label                  { font-size: 12px; line-height: 16px;}
    .common-title-size  { font-size: 36px; line-height: 45px;}
}