.table-filter {
  .form-group { 
    margin-right: 30px;
    &.search-product {
      position: relative;
      img { width: 20px; position: absolute; top: 11px; left: 15px;}
      .form-control { width: 280px; padding-left: 40px;}
    }
    .form-control { width: 160px; padding: 8px 15px;}
    label { margin-bottom: 0; margin-right: 12px; text-transform: capitalize; color: $color_grey;}
  }
  .normal-dropdown {
    width: 150px;
    .css-yk16xz-control, .css-1pahdxg-control {
      min-height: 40px !important;
    }
    .css-yk16xz-control .css-g1d714-ValueContainer, 
    .css-1pahdxg-control .css-g1d714-ValueContainer { padding: 3px 15px;}
  }
  .view-select .normal-dropdown { width: 90px;}
  .date-range-picker {
    .form-control {
      width: 100%;
      .date-range-input {
        input { padding: 8px 40px 8px 15px;}
        &::after { top: 9px;}
      }
    }
  }
  .btn-1 { margin-left: 25px; padding: 10px 25px;}
}