.signup-sec { 
    h2 { 
        padding-bottom: 25px;
    }
    .checkbox-style label{
        margin-bottom: 0;
        text-transform: none;
    }
    .nav-link { padding: 0px 5px; font-weight: $font_weight_600; text-decoration: underline; color: $color_blue;} 
}
.date-field {
    .react-date-picker { max-height: 44px;}
    .react-date-picker__wrapper {
        border: none;
        .react-date-picker__clear-button { display: none;}
        .react-date-picker__calendar-button { 
            svg { opacity: 0;}
            &::before { content: ''; background: url('../../images/calender-ic.svg') no-repeat; height: 24px; width: 24px; position: absolute; top: 12px; right: 12px; display: block;}
        }
        .react-date-picker__inputGroup__input {
            outline: none;
        }
    }
    .react-date-picker__calendar {
        z-index: 1000;
        .react-calendar {
            margin-top: 5px;
            border: none;
            border-radius: 10px;    
            box-shadow: 0px 0px 8px #ddd;
            .react-calendar__viewContainer { padding: 10px;}
            .react-calendar__tile--active,
            .react-calendar__tile--hasActive,
            .react-calendar__tile--now {
                background: $color_blue;
                border-radius: 4px;
                color: $color_white;
            }     
            .react-calendar__navigation button:enabled:hover, 
            .react-calendar__navigation button:enabled:focus,
            .react-calendar__tile:enabled:hover, 
            .react-calendar__tile:enabled:focus  { background: #f4f4f4; border-radius: 4px; color: #000;}
            .react-calendar__navigation { height: 35px;}
            .react-calendar__year-view .react-calendar__tile, 
            .react-calendar__decade-view .react-calendar__tile, 
            .react-calendar__century-view .react-calendar__tile { padding: 15px 0;}
        }
    }
}
.rcs-custom-scroll{
    .panel-content-custom {
        margin: 0px auto;
        padding: 0px 5px;
        max-height: calc(100vh - 210px);
        width: 440px;
    }
    .rcs-inner-handle { background: $color_blue !important;}

    @media (min-width: 1200px) and (max-width: 1499px) { 
        .panel-content-custom { width: 100%;}
    }
}
.role-select { 
    li {
        position: relative;
        display: inline-block;
        width: calc(100% / 2 - 8px);
        margin-right: 15px;
        vertical-align: middle;
        &:last-child { margin: 0;}
        input[type="radio"]{ 
            -webkit-appearance: none;
            -moz-appearance: none;
            -ms-appearance: none;
            -o-appearance: none;
            appearance: none;
            outline: none;
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            border: 1px solid #D8DDE6;
            border-radius: 8px;
            transition: all 0.15s ease-out 0s;
            cursor: pointer;
            z-index: 1000;
            &:checked, &:hover { border-color: $color_red;}
            &:checked { 
                &::before { content: ''; background: url('../../images/ic_done.svg') no-repeat; height: 14px; width: 14px; position: absolute; top: 10px; right: 10px; display: block;}
            }
        }
        label{
            margin: 0;
            padding: 18px 12px;
            width: 100%; 
            background: #fff;
            min-height: 72px;
            color: $color_light_black !important;
            font-size: 14px;
            line-height: 18px;
            font-weight: $font_weight_500;
            text-align: left;
            display: flex; display: -webkit-flex;
            align-items: center; -webkit-align-items: center;
            justify-content: center; -webkit-justify-content: center;
            img { margin-right: 22px; width: 35px; position: absolute; top: 50%; left: 15px; transform: translateY(-50%); -webkit-transform: translateY(-50%);}
        }
    }    
    @media (min-width: 1200px) and (max-width: 1499px) { 
        li { 
            label 
            { 
                img { width: 28px;}
            }
        }
    }
    @media ( max-width: 767px) {
        li {
            label {
                padding: 12px;
                min-height: 60px;
                img { left: 10px; width: 22px;}
            }
        }
    }
}

