.mywish-list-sec {
  h1 { padding-bottom: 0;}
  .btn-text {
    padding: 0 0 0 15px;
    font-size: 14px;
    line-height: 18px;
    color: $color_red !important;
    font-weight: $font_weight_500;
    text-transform: none;
    &:hover { color: $color_blue !important; text-decoration: none;}
  }
  .btn-outline.add-cart {
      margin-top: 16px;
      padding: 8px 18px;
  }
  @media ( max-width: 767px) {
    .section-title {
      font-size: 20px; line-height: 25px;
    }
  }
}