.overlay { 
  overflow: hidden; 
  position: relative;
  &::before {
    content: '';
    background: rgba(0,0,0,0.5);
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
  }
}
.product-listing-page {
  padding: 15px 15px 50px 30px;
  .filter-ic { display: none;}
  .filters-blk { width: 380px; flex-basis: 380px; -webkit-flex-basis: 380px;}
  .filters-sec {
      background: #f9f9f9;
      border-radius: 8px;
      .filter-title {
        padding: 15px 30px 12px 30px;
        border-bottom: 4px solid $color_light_black;
        h6 { 
          font-size: 14px;
          line-height: 19px;
          font-weight: $font_weight_700;
          color: $color_light_black;
          text-transform: uppercase;
        }
        a { display: none;}
      }
      .filter-cards {
        padding: 5px 30px;
        .card {
          margin: 15px 0;
          padding: 0;
          border: none;
          border-radius: 8px;
          box-shadow: 0px 2px 12px rgba(0,0,0,0.04);
          .card-body {
            padding: 0;
            .card-title {
              margin: 0;
              padding: 15px 15px 12px 15px;
              border-bottom: 1px solid #DADADA;
              h6 { color: $color_light_black;
                font-weight: $font_weight_600;}
            }
            .card-text{
              .filter-select {
                padding: 15px;
                .btn {
                  margin: 6px 8px;
                  padding: 8px;
                  background: transparent;
                  border-color: #dadada;
                  border-radius: 16px;
                  font-size: 14px;
                  line-height: 18px;
                  text-transform: capitalize;
                  color: $color_light_black;
                  font-weight: $font_weight_500;
                  &:hover, &:focus { 
                    border-color: $color-red;
                    color: $color-red;
                    box-shadow: none;
                  }
                }
                .seemore-link { 
                  padding: 15px 8px 10px;
                  color: $color-grey;
                  text-transform: uppercase;
                  letter-spacing: 1px;
                  font-size: 14px;
                  line-height: 19px;
                  font-weight: $font_weight_600;
                  display: inline-block;
                }
              }
            }
          }
        }
      }
  }
  .product-list-blk {
    padding: 25px 25px 25px 35px;
    width: calc(100% - 380px);
    flex-basis: calc(100% - 380px); -webkit-flex-basis: calc(100% - 380px);
    h2{
      font-size: 48px;
      line-height: 58px;
    }
    .sorting-blk {
      .product-search {
        position: relative;
        padding-right: 35px;
        input {
          padding: 12px 10px 8px 25px;
          background: transparent;
          border-radius: 0 !important;
          border: none;
          border-bottom: 1px solid $color_light_black;
          font-size: 14px;
          line-height: 19px;
          font-weight: $font_weight_500;
          width: 280px;
          &::placeholder{
            color: $color-grey;
          }
          &::-moz-placeholder{
              color: $color-grey;
          }
          &::-webkit-input-placeholder{
              color: $color-grey;
          }
          &:-ms-input-placeholder{
              color: $color-grey;
          }
          &::-o-placeholder{
              color: $color_light-black;
          }
          &:focus { border-color: $color-blue !important;}
        }
        &::before { content: ''; background: url('../../images/search-grey-ic.svg') no-repeat; height: 17px; width: 17px; position: absolute; top: 12px; left: 0px; display: block; }
      }
      .sorting-dropdown {
        .select-style {
          @include select_style;
          width: 197px;
          .css-1pahdxg-control { 
            border: 1px solid $color-blue;
            .css-1hb7zxy-IndicatorsContainer { 
              transform: rotate(0);
              -webkit-transform: rotate(0);
              }
          }
          .css-yk16xz-control {
            border: 1px solid #dadada;
            &:focus, &:hover { border-color: $color-blue;}
          }
          .css-1wa3eu0-placeholder {
            font-size: 14px !important;
            line-height: 19px;
            text-transform: uppercase;
            font-weight: $font_weight_600;
            color: $color_light-black;
          }
          .css-tlfecz-indicatorContainer,
          .css-1pahdxg-control {
            svg {
              display: none;
            }
            &::before { content: ''; background: url('../../images/sorting-ic.svg') no-repeat; height: 24px; width: 24px; position: absolute; top: 15px; right: 5px; display: block;}
          }
        }
      }
    }
    .filter-applied {
      padding: 15px 0;
      p { padding-top: 15px; width: 85px;}
      .filter-blk {
        margin: 0 0 15px 15px;
        padding: 8px 10px;
        border: 1px solid #dadada;
        border-radius: 32px;
        h6{
          font-size: 12px;
          line-height: 15px;
          font-weight: $font_weight_600;
          span {
            margin: 0px 10px;
            color: $color-red;
          }
          .color-bg { 
            margin-right: 0;
            height: 20px;
            width: 20px;
            border-radius: 50px;
            display: inline-block;
            vertical-align: middle;
          }
        }
        .btn {
          padding: 0;
          background: transparent;
          border: none;
        }
      }
    }
  }

  /* Responsive */
  @media (min-width: 1200px) and (max-width: 1499px) { 
    padding: 15px;
    .filters-blk {
      width: 330px; flex-basis: 330px; -webkit-flex-basis: 330px;
      .filters-sec {
        .filter-cards {
          padding: 5px 15px;
          min-height: 650px;
          .card {
            .card-body {
              .card-title { padding: 10px 15px;}
              .filter-select {
                padding: 10px 5px;
                .btn {
                  margin: 6px;
                  font-size: 13px;
                  line-height: 16px;
                }
                .seemore-link { padding: 10px;}
              }
            }
          }
        }
      }
    }
    .product-list-blk {
      padding: 15px 15px 15px 25px;
      width: calc(100% - 330px); flex-basis: calc(100% - 330px); -webkit-flex-basis: calc(100% - 330px);
      h2                              { padding-right: 15px; font-size: 35px; line-height: 42px;}
      .filter-applied {
        padding-bottom: 0;
        p { padding-top: 10px;}
        .filter-blk {
          padding: 5px 10px;
          display: inline-flex; display: -webkit-inline-flex;
        }
      }
    }
  }

  @media (min-width: 992px) and (max-width: 1199px) { 
    padding: 15px;
    .filters-blk {
      width: 330px; flex-basis: 330px; -webkit-flex-basis: 330px;
      .filters-sec {
        .filter-cards {
          padding: 5px 15px;
          min-height: 650px;
          .card {
            .card-body {
              .card-title { padding: 10px 15px;}
              .filter-select {
                padding: 10px 5px;
                .btn {
                  margin: 6px;
                  font-size: 13px;
                  line-height: 16px;
                }
                .seemore-link { padding: 10px;}
              }
            }
          }
        }
      }
    }
    .product-list-blk {
      padding: 15px 15px 15px 25px;
      width: calc(100% - 330px); flex-basis: calc(100% - 330px); -webkit-flex-basis: calc(100% - 330px);
      h2                              { padding-right: 15px; font-size: 22px; line-height: 28px;}
      .product-listing .col           { width: calc(100% / 2); flex-basis: calc(100% / 2); -webkit-flex-basis: calc(100% / 2);} 
      .sorting-blk {
        .product-search {
          input                       { width: 100%;}
        }
      }
      .filter-applied {
        padding-bottom: 0;
        p { padding-top: 10px;}
        .filter-blk {
          padding: 5px 10px;
          display: inline-flex; display: -webkit-inline-flex;
        }
      }
    }
  }

  @media (min-width: 768px) and (max-width: 991px) { 
    padding: 15px;
    > .d-flex { display: block !important;}
    .filters-blk {
      width: 100%; flex-basis: 100%; -webkit-flex-basis: 100%; position: relative;
      .filter-ic { 
        padding: 5px 10px;
        display: block;
        background: $color-red;
        a { color: $color_white !important; display: block;}
        position: fixed;
        width: 100%;
        bottom: 0;
        left: 0;
        z-index: 10;
        text-align: center;
        font-weight: $font_weight_600;
      }
      .mobile-filter { 
        position: fixed;
        top: 0;
        left: -100%;
        width: 450px;
        height: 100%;
        max-width: 0;
        display: block;
        z-index: 1060;
        transition-duration: .3s;
        border-radius: 0;
        overflow-y: auto;
        &.show {
          max-width: 100%;
          left: 0;
        }
        .filter-cards {
          padding: 5px 15px;
          min-height: 650px;
          .card {
            .card-body {
              .card-title { padding: 10px 15px;}
              .filter-select {
                padding: 10px 5px;
                .btn {
                  margin: 6px;
                  font-size: 13px;
                  line-height: 16px;
                }
                .seemore-link { padding: 10px;}
              }
            }
          }
        }
        .filter-title {
          padding: 10px 15px;
          a { display: block;}
        }
      }
    }
    .product-list-blk {
      padding: 0px;
      width: 100%; flex-basis: 100%; -webkit-flex-basis: 100%;
      h2                              { font-size: 28px; line-height: 35px;}
      .product-listing .col           { width: calc(100% / 2); flex-basis: calc(100% / 2); -webkit-flex-basis: calc(100% / 2);} 
      .filter-applied {
        padding-bottom: 0;
        p { padding-top: 10px;}
        .filter-blk {
          padding: 5px 10px;
          display: inline-flex; display: -webkit-inline-flex;
        }
      }
    }
  }

  @media ( max-width: 767px) {
    padding: 15px;
    > .d-flex { display: block !important;}
    .filters-blk {
      width: 100%; flex-basis: 100%; -webkit-flex-basis: 100%; position: relative;
      .filter-ic { 
        padding: 5px 10px;
        display: block;
        background: $color-red;
        a { color: $color_white !important; display: block;}
        position: fixed;
        width: 100%;
        bottom: 0;
        left: 0;
        z-index: 10;
        text-align: center;
        font-weight: $font_weight_600;
      }
      .mobile-filter { 
        position: fixed;
        top: 0;
        left: -100%;
        width: 330px;
        height: 100%;
        max-width: 0;
        display: block;
        z-index: 1060;
        transition-duration: .3s;
        border-radius: 0;
        overflow-y: auto;
        &.show {
          max-width: 100%;
          left: 0;
        }
        .filter-cards {
          padding: 5px 15px;
          min-height: 650px;
          .card {
            .card-body {
              .card-title { padding: 10px 15px;}
              .filter-select {
                padding: 10px 5px;
                .btn {
                  margin: 6px;
                  font-size: 13px;
                  line-height: 16px;
                }
                .seemore-link { padding: 10px;}
              }
            }
          }
        }
        .filter-title {
          padding: 10px 15px;
          a { display: block;}
        }
      }
    }
    .product-list-blk {
      .d-flex, 
      > .justify-space-between          { display: block !important;}
      padding: 0px;
      width: 100%; flex-basis: 100%; -webkit-flex-basis: 100%;
      h2                              { font-size: 22px; line-height: 28px;}
      .product-listing .col           { width: 100%; flex-basis: 100%; -webkit-flex-basis: 100%;} 
      .sorting-blk {
        .product-search {
          margin-bottom: 15px;
          padding: 0;
          input                       { width: 100%; font-size: 12px;}
        }
        .sorting-dropdown {
          .select-style {
            width: 100%;
          }
        }
      }
      .filter-applied {
        p { padding: 0 0 15px 0;}
        .filter-blk {
          margin: 0 0 12px 0;
          padding: 4px 10px;
          display: inline-flex; display: -webkit-inline-flex;
        }
      }
    }
  }
}
.product-list-pagination {
  margin-top: 15px;
  li { 
    display: inline-block;
    padding: 0px 10px;
    &.active a {
      color: $color-blue;
    }
    a{
      font-size: 14px;
      line-height: 19px;
      color: $color_light_black;
      font-weight: $font_weight_600;
      text-transform: uppercase;
    }
  }
  @media ( max-width: 767px) { 
    margin: 0;
    display: block;
    text-align: center;
    h5 { padding-bottom: 10px;}
  }
}
/* Color Pallete */
.color-pallete {
  button.btn {
    margin: 9px !important;
    height: 40px;
    width: 40px;
    border: none;
    border-radius: 50px !important;
    box-shadow: none !important;
    position: relative;
    &::before {
      content: '';
      border: 1px solid #dadada;
      height: calc(100% + 8px);
      width: calc(100% + 8px);
      border-radius: 50%;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition-duration: .5s;
      -webkit-transition-duration: .5s;
    }
    &:hover::before, &:focus::before { border-color: #000000;}
    &:focus::after {
      content: '';
      background: #fff;
      height: 12px;
      width: 12px;
      border-radius: 50%;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
  @media ( max-width: 767px) {
    button.btn {
      margin: 7px !important;
      height: 30px;
      width: 30px;
    }
  }
}
/* Other */
@media (min-width: 768px) and (max-width: 991px) { 
    .filter-btn {
    .footer-bottom { padding-bottom: 50px;}
  }
}
@media ( max-width: 767px) { 
  .filter-btn {
    .footer-bottom { padding-bottom: 50px;}
  }
}