.overflow-hidden { overflow: hidden;}
.login-sec{
    padding-top: 100px; 
    background: #fff;
    .full-height { 
      height: calc(100vh - 100px); max-height: 950px;
    }
    .login-left{
      padding: 35px;
      background: #0566FF;
      width: 65%; flex-basis: 65%; flex-basis: 65%;
      img { width: 100%;}
      h3 { padding-top: 25px; color: $color_white; font-style: italic;}
      @media (max-height: 600px) {
        img { width: 60%;}
      }
    }
    .login-right{
      padding: 35px;
      width: 35%; flex-basis: 35%; flex-basis: 35%;
      p{
          font-size: 12px;
          line-height: 15px;
          font-weight: 600;
          text-transform: uppercase;
          text-align: right;
          width: 100%;
          a { 
              color: #0566FF;
          &:hover { text-decoration: underline;}
          }
      }
      .login-blk{
        margin: 0px auto;
        padding: 25px 0;
        height: 100%;
        .nav-tabs {
          padding-bottom: 25px;
          border: none;
          justify-content: center; -webkit-justify-content: center;
          .nav-item{
              padding: 10px 14px;
              position: relative;
              &:last-child { 
                  &::before { display: none;}
              }
              &::before { content: ''; background: #D8D8D8; width: 1px; height: 45px; position: absolute; top: 0; right: 0; transform: skewX(-15deg); -webkit-transform: skewX(-15deg);}
              .nav-link{
                  padding: 0;
                  font-size: 18px; 
                  line-height: 24px;
                  color: #878687;
                  text-transform: capitalize;
                  font-weight: 600;
                  border: none;
                  cursor: pointer;
                  &.active, &:hover { color: $color_light-black !important;}
              }
          }
          &.business-tab{
            justify-content: space-between;
            -webkit-justify-content: space-between;
            .nav-item{
              padding: 10px 0;
              &::before{
                content: none;
              }
              .nav-link{
                color: #878687 !important;
                font-weight: $font_weight_700;
                text-decoration: none;
                font-size: 14px;
                line-height: 18px;
                .badge {
                  margin-right: 10px;
                  padding: 10px;
                  height: 28px;
                  width: 28px;
                  border-radius: 50%;
                  background-color: #DADADA;
                }
                &.active{
                  color: $color_light-black !important;
                  .badge{
                    background-color: $color_blue;
                    color: $color_white;
                  }
                }
              }
            }
          }
        }
        form { 
            width: 100%;
        }
        button.login-btn {
            padding: 15px 25px;
            background: $color_light-black;
            width: 100%;
            border-radius: 24px;
            color: $color_white;
            font-size: 14px;
            line-height: 16px;
            font-weight: 700;
            text-transform: uppercase;
            border: 1px solid $color_light-black;
            &:hover { background: $color_blue; border-color: $color_blue;}
        }
        .rcs-custom-scroll{
          .panel-content-custom {
            padding: 0px 25px;
          }
        }
      }
    }
  &.select-package{
    .full-height{
      height: auto;
      max-height: initial;
    }
    .nav-tabs{
      margin: 0 auto;
      max-width: 400px;
    }
    .login-right{
      width: 100%;
      flex-basis: 100%;
      -webkit-flex-basis: 100%;
      h2{
        text-align: center;
      }
      .rcs-custom-scroll .panel-content-custom{
        width: 100%;
      }
    }
    .packages-list{
      .card {
          padding: 24px 15px 30px;
          border-radius: 12px;
          box-shadow: 6px 6px 50px rgba(0,0,0,0.08);
          min-height: 480px;
          .card-body{
            display: flex;
            flex-direction: column;
          }
          .card-subtitle{
            margin-bottom: 16px;
            text-align: left;
            text-transform: capitalize;
            color: $color_blue;
          }
          .card-title{
            margin-bottom: 20px;
            padding-bottom: 0;
            text-align: left;
          }
          .list-group .list-group-item {
            padding: 0 0 12px;
            font-size: 16px;
            line-height: 26px;
            font-weight: $font_weight_500;
            border: none;
            img{
              margin-right: 5px;
            }
            span{
              display: inline-block;
              width: calc(100% - 31px);
              vertical-align: top;
            }          
          }
          .btn{
            margin-top: auto;
            align-self: center;
            text-transform: capitalize;
          }
        }
        .see-more.nav-link{
          margin-top: 20px;
          text-transform: uppercase;
          color: $color_light-black !important;
          font-weight: 700;
        }
    }
    .package-steps {
        display: flex;
        margin: 120px -35px 0;
        background-color: $color_blue;
        color: #fff;
        .step {
          padding: 80px 65px;
          flex: 1;
          text-align: center;
          position: relative;
          &:not(:last-child)::after{
            content: "";
            display: inline-block;
            width: 64px;
            height: 100%;
            background: url('../../images/step-shape.svg') no-repeat center / cover;
            position: absolute;
            top: 0;
            right: -32px;
          }
          img {
              margin: 0 auto 30px;
              max-width: 80px;
          }
          h3 {
              margin-bottom: 12px;
          }
          h4 {
              font-weight: $font_weight_500;
              line-height: 30px;
              font-size: 20px;
          }
        }
    }
    .package-features {
        margin-top: 120px;
        h3{
          padding: 15px 24px;
          line-height: 30px;
        }
        .features-list{
          border: 1px solid #dadada;
          border-right: none;
          border-radius: 20px 0 0 20px;
          .list-group-item{
            padding: 20px 24px;
            font-size: 16px;
            line-height: 30px;
            font-weight: $font_weight_500;
            border-color: #dadada;
          }
        }
        .package-feature_individual{
          h6{
            padding: 20px;
            background-color: $color_light-black;
            text-align: center;
            font-size: 16px;
            line-height: 19px;
            font-weight: $font_weight_500;
            color: $color_white;
            border-radius: 12px 12px 0 0;
          }
          .list-group{
            border-top: 1px solid #dadada;
            border-bottom: 1px solid #dadada;
            .list-group-item{
              padding: 20px 0;
              text-align: center;
              color: #B9B9B9;
              border-color: #dadada;
              img{
                max-width: 30px;
              }
              .fa{
                display: flex;
                height: 30px;
                justify-content: center;
                align-items: center;
              }
            }
          }
          &.last{
            .list-group{
              border-right: 1px solid #dadada;
              border-radius: 0 12px 12px 0;
            }
          }
          &:hover{
            box-shadow: 8px 8px 20px rgba(0,0,0,0.10);
            h6{
              background-color: $color_blue;
            }
          }
        }
        .learn-more {
            margin-top: 40px;
            text-transform: uppercase;
            font-weight: 700;
        }
    }
    .package-benefits {
        margin-top: 120px;
        text-align: center;
        div[class^="col-"] {
            margin-bottom: 30px;
        }
        img {
          margin: 0 auto 20px;
          max-width: 80px;
        }
        h3 {
          margin-bottom: 16px;
          font-weight: 700;
        }
        p {
          font-size: 16px;
          line-height: 26px;
          font-weight: 500;
          text-transform: initial;
          text-align: center;
        }
    }
    .faq{
      margin-top: 30px;
      .faq-item{
        border-bottom: 1px solid #dadada;
        h6 {
          padding: 25px 0;
          font-weight: $font_weight_700;
          cursor: pointer;
          &::after{
            content: "";
            display: block;
            background: url('../../images/faq-ic.svg') no-repeat top center;
            height: 20px;
            width: 20px;
            float: right;
          }
        }
        p{
          font-weight: $font_weight_500;
          text-align: left;
          text-transform: initial;
          font-size: 16px;
          line-height: 26px;
        }
      }
    }
    .customer-testimonials {
        margin-top: 120px;
        p{
          margin: 0;
          padding-bottom: 20px;
          font-size: 16px;
          line-height: 26px;
          font-weight: $font_weight_500;
          text-transform: initial;
          text-align: left;
          border-bottom: 1px solid #dadada;
          position: relative;
          &::before{
            content: '';
            background: url('../../images/inverted-comma.svg') no-repeat center;
            width: 100px;
            height: 80px;
            position: absolute;
            top: -20px;
            left: -20px;
          }
          span{
            position: relative;
          }
        }
        .customer-details {
            display: flex;
            margin-bottom: 18px;
            padding-top: 24px;
            align-items: center;
            img{
              margin-right: 20px;
              height: 55px;
              width: 55px;
              border-radius: 50%;
              object-fit: cover;
              object-position: center;
            }
        }
    }
    .full-width-center > .row {
        width: 100%;
        justify-content: center;
    }
    .package-conditions{
      margin-top: 50px;
      .btn{
        max-width: 60%;
      }
    }
  }
  &.profile-setup{
    h6{
      font-weight: $font_weight_600;
    }
  }
  .forgot-link{
    padding: 0;
    font-weight: 600;
    color: $color_light-black;
  }
  .reacaptcha { 
    padding: 25px 0;
    text-align: center;
  }
  .social-login{
    padding-top: 25px;
    text-align: center;
    h6 {
        padding: 0px 40px;
        font-size: 12px;
        line-height: 15px;
        color: $color_light-black;
        font-weight: 500;
        background: #fff;
        display: inline-block;
        position: relative;
        z-index: 1;
    }
    .title-seprator {
        position: relative;
        &::before { content: ''; background: #DADADA; width: 100%; height: 1px; position: absolute; top: 12px; left: 0;}
    }
    .social-ic { 
        a { padding: 10px;}
    }
  }
  /* Responsive Design */
  @media ( min-width: 1200px) and (max-width: 1499px) {
    .login-right {
      p, h2 { text-align: center;}
      .login-blk {
        padding: 15px 0;
        .nav-tabs {
          .nav-item {
            padding: 10px;
            .nav-link {
              font-size: 16px;
            }
          }
        }
        .rcs-custom-scroll{
          .panel-content-custom {
            padding: 0px 10px;
          }
        } 
      }
    }
  }

  @media ( min-width: 992px) and (max-width: 1199px) { 
    .login-left {
      width: 50%; flex-basis: 50%; -webkit-flex-basis: 50%;
    }
    .login-right {
      width: 50%; flex-basis: 50%; -webkit-flex-basis: 50%;
    }
  }

  @media ( min-width: 768px) and (max-width: 991px) { 
    padding-top: 130px;
    .full-height { height: 100%; max-height: 100%; display: block !important;}
    .login-left {
      padding: 25px;
      width: 100%; flex-basis: 100%; -webkit-flex-basis: 100%;
      h3 { padding-top: 15px;}
    }
    .login-right {
      padding: 25px;
      width: 100%; flex-basis: 100%; -webkit-flex-basis: 100%;
      h6 { padding: 8px 0 15px;}
      .login-blk {
        padding: 5px 0;
        .nav-tabs {
          padding-bottom: 15px;
          .nav-item { 
            .nav-link { font-size: 16px; line-height: 25px;}
            &::before { height: 28px; top: 8px;}
          }
        }
        button.login-btn { padding: 12px 15px;}
      }
    }
    .rcs-custom-scroll{
      .panel-content-custom {
        padding: 0 !important;
        max-height: 100%;
      }
    } 
    .reacaptcha { padding: 15px 0;}
    .social-login {
      h6 { padding: 0px 35px;}
    }
  }

  @media ( max-width:767px){
    padding-top: 124px;
    .full-height { height: 100%; max-height: 100%; display: block !important;}
    .login-left {
      padding: 15px;
      width: 100%; flex-basis: 100%; -webkit-flex-basis: 100%;
      h3 { padding-top: 15px;}
    }
    .login-right {
      padding: 15px;
      width: 100%; flex-basis: 100%; -webkit-flex-basis: 100%;
      h2,h6 { text-align: center !important;}
      p { text-align: center; font-size: 11px;}
      h6 { padding: 8px 0 15px;}
      .login-blk {
        padding: 5px 0;
        .nav-tabs {
          padding-bottom: 15px;
          .nav-item { 
            padding: 10px;
            .nav-link { font-size: 14px; line-height: 22px;}
            &::before { height: 28px; top: 7px;}
          }
        }
        button.login-btn { padding: 12px 15px;}
      }
    }
    .rcs-custom-scroll{
      .panel-content-custom {
        padding: 0 !important;
        width: 100%;
        max-height: 100%;
      }
    } 
    .forgot-link { font-size: 12px; line-height: 16px;}
    .reacaptcha { padding: 15px 0;}
    .social-login {
      h6 { padding: 0px 35px;}
    }
  }
}
.checkbox-style { 
  label { margin-bottom: 0; padding-left: 35px !important; text-transform: capitalize; line-height: 22px; font-weight: 600; color: #878687;}
  [type="checkbox"]:not(:checked),
  [type="checkbox"]:checked {
  position: absolute;
  left: -9999px;
  }
  [type="checkbox"]:not(:checked) + label,
  [type="checkbox"]:checked + label {
  position: relative;
  padding-left: 1.95em;
  cursor: pointer;
  }
  /* checkbox aspect */
  [type="checkbox"]:not(:checked) + label:before,
  [type="checkbox"]:checked + label:before {
  content: '';
  position: absolute;
  left: 0; top: 0;
  width: 24px; height: 24px;
  border: 1px solid #DADADA;
  background: #fff;
  border-radius: 2px;
  }
  [type="checkbox"]:checked + label::before { background: #0566FF; border: none;}
  /* checked mark aspect */
  [type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
  content: '\2713\0020';
  position: absolute;
  top: 4px; left: 5px;
  font-size: 1.3em;
  line-height: 0.8;
  color: $color_white;
  transition: all .2s;
  font-family: 'Lucida Sans Unicode', 'Arial Unicode MS', Arial;
  }
  /* checked mark aspect changes */
  [type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0);
  }
  [type="checkbox"]:checked + label:after {
  opacity: 1;
  transform: scale(1);
  }
  @media ( max-width:767px){ 
  /* checkbox aspect */
  [type="checkbox"]:not(:checked) + label:before,
  [type="checkbox"]:checked + label:before { 
    height: 20px; width: 20px;
    }
    label { padding-left: 25px !important;}
  }
}
/* Country Code Select */
.country-code-list {
  .react-tel-input {
    font-family: 'Montserrat', sans-serif !important;
    .form-control {
      padding: 10px 15px 10px 55px;
      width: 100%;
      height: auto;
      background: #f4f4f4;
      border: none;
      color: $color_light-black;
    }
   .flag-dropdown {
     border: none;
     border-radius: 24px;
     &.open { 
       background: #f4f4f4; border-radius: 24px 0 0 24px;
       .selected-flag { background: #f4f4f4; border-radius: 24px 0 0 24px;}
      }
     .selected-flag { 
       padding-left: 15px;
       .flag { margin-top: -11px;}
      }
     .selected-flag:hover, .selected-flag:focus {
      background: #f4f4f4 !important;
      border-radius: 24px 0 0 24px;
    }
   }
  .country-list {
    li { 
      display: block; font-size: 14px; line-height: 19px;
      span {
        font-weight: $font_weight_400; color: $color_light-black;
      } 
    }
    .country {
      &.highlight, &:hover {
        background: #fafafa;
        }
      } 
    } 
    @media (max-width: 767px) {
      .country-list {
        width: 270px;
      }
    }
  }
}
