.project-details-sec {
  .tshirt-sides-img { padding: 0;}
  .artist-project-header {
    margin-bottom: 25px;
    padding-bottom: 25px;
    border-bottom: 1px solid #dadada;
    .product-nm h5 { padding-top: 10px;}
    .edit-project {
      img { margin-right: 8px; width: 22px;}
      div { margin-left: 50px;}
      .btn { 
        padding: 12px 60px;
        background: $color_light_black;
        &:hover { background: $color_blue;}
      }
    }
  }
  .artist-style-nm {
    li {
      margin-bottom: 15px;
      width: calc(100% / 2 - 15px);
      padding: 15px 10px 15px 50px;
      margin-right: 15px;
      border: 1px solid #dadada;
      border-radius: 8px;
      font-size: 12px;
      font-weight: $font_weight_500;
      display: inline-block;
      position: relative;
      img { margin-right: 15px; width: 35px; position: absolute; top: 50%; left: 10px; transform: translateY(-50%); -webkit-transform: translateY(-50%);}
      &:hover { border-color: $color_red;}
    }
    &.artist-type {
      li {
        padding: 10px 10px 10px 45px;
        border-radius: 24px;
        img { 
          width: 28px;
          &.tank-img { width: 20px;}
        }
      }
    }
    &.size-select {
      li{
        padding: 0;
        margin-bottom: 0;
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
  .tags {
    li { 
      margin-right: 15px;
      display: inline-block;
      font-weight: $font_weight_500;
    }
  }
  .artist-pro-desc {
    p {
      margin-bottom: 0;
      font-size: 14px;
      line-height: 22px;
    }
  }
}
.reviews-info a {
  padding-top: 10px;
  font-weight: $font_weight_700;
  text-transform: uppercase;
}
.reply-review-modal {
  min-width: 800px;
  .reviews-blk{
    margin-top: 10px;
    .reviews-info {
      border: none;
    }
  }
}