@mixin common_btn {
  display: inline-block;
  padding: 14px 24px;
  font-size: 15px;
  line-height: 19px;
  font-weight: $font_weight_700;
  color: $color_white;
  background-color: $color_red;
  border-radius: 4px;
  border: 1px solid $color_red;
}

@mixin common_btn_color_blue {
  @include common_btn;
  color: $color_white;
  background: $color_blue;
  border-color: $color_blue;
  &:hover,&:focus{
      background: $color_red;
      border-color: $color_red;
  }
}
@mixin btn_1 {
  padding: 15px 35px;
  display: inline-flex;
  display: -webkit-inline-flex;
  align-items: center;
  -webkit-align-items: center;
  justify-content: center;
  -webkit-justify-content: center;
  background: $color_blue;
  color:$color_white;
  border-radius: 28px;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  font-weight: $font_weight_700;
  border: none;
  &:hover  { background: $color_light-black; color: $color_white !important;}
  @media (min-width: 768px) and (max-width: 991px) { 
    padding: 12px 35px;
    }
  @media (max-width: 767px) {
      padding: 12px 30px;
  }
}

@mixin btn_outline {
  padding: 15px 35px;
  display: inline-flex;
  display: -webkit-inline-flex;
  align-items: center;
  -webkit-align-items: center;
  justify-content: center;
  -webkit-justify-content: center;
  background: transparent;
  color: #000000;
  border: 1px solid #464546;
  border-radius: 28px;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: $font_weight_700;
  &:hover, &:focus { background: $color_blue; color: $color_white !important; border-color: $color_blue !important; box-shadow: none !important;}
  @media (min-width: 768px) and (max-width: 991px) { 
    padding: 12px 35px;
    }
  @media (max-width: 767px) {
    padding: 12px 30px;
    }
}

@mixin btn_text {
    padding:0; font-size: 12px; line-height: 15px; background: transparent !important; border: none; color: $color_blue !important; font-weight: $font_weight_600; text-transform: uppercase; vertical-align: top;
    &:hover { text-decoration: underline;}
}

@mixin form_control{
  padding: 10px 15px;
  color: $color_light-black;
  height: auto;
  border: 1px solid transparent;
  font-size: 14px;
  line-height: 22px;
  border-radius: 24px !important;
  background: $color_light;
  font-weight: $font_weight_500;
  color: $color_light-black;
  &::placeholder{
      color: #b9b9b9;
  }
  &::-moz-placeholder{
      color: #b9b9b9;
  }
  &::-webkit-input-placeholder{
      color: #b9b9b9;
  }
  &:-ms-input-placeholder{
      color: #b9b9b9;
  }
  &::-o-placeholder{
      color: #b9b9b9;
  }
  &:focus{
      background: $color_light;
      box-shadow: none;
      border-color: $color_blue;
  }
  @media (max-width: 767px) { 
      padding: 8px 15px;
  }
}

@mixin select_style{
  .css-1pahdxg-control { 
      box-shadow: none; border: none; background: transparent; min-height: 44px !important;
      .css-g1d714-ValueContainer { 
        padding: 4px 10px;
        display: inline-block;
      }
      .css-b8ldur-Input { padding: 0; opacity: 0;}
      .css-1g6gooi { opacity: 0;}
      .css-1hb7zxy-IndicatorsContainer { 
        transform: rotate(180deg);
        -webkit-transform: rotate(180deg);
        }
  }
  .css-yk16xz-control { 
      background: transparent; border: none; min-height: 44px !important;
      .css-g1d714-ValueContainer { 
          padding: 4px 10px;
          display: inline-block;
          &:focus { outline: none;}
      }
      .css-b8ldur-Input { padding: 0; opacity: 0;}
      .css-1g6gooi { opacity: 0;}
  }
  .css-1wa3eu0-placeholder,
  .css-1uccc91-singleValue { font-size: 14px; color: #464546 !important; font-weight: $font_weight_600 !important;}
  .css-1okebmr-indicatorSeparator { display: none !important;}
  .css-1hb7zxy-IndicatorsContainer { padding: 0; transition-duration: .3s; -webkit-transition-duration: .3s;}
   svg path { stroke: #000; fill: #000;}
  .css-26l3qy-menu,.css-1w9j89e-menu { 
    .css-4ljt47-MenuList {
        padding: 0 !important;
    }
    .css-yt9ioa-option,
    .css-9gakcf-option,
    .css-1n7v3ny-option,
    .css-yt9ioa-option {
        font-size: 14px;
        font-weight: $font_weight_500;
    }
    .css-1n7v3ny-option,
    .css-9gakcf-option {
        background-color: $color_blue !important;
        color: $color_white;
    }
  }
  @media ( max-width: 767px) {
    .css-1pahdxg-control { 
      min-height: 38px !important;
    }
    .css-yk16xz-control { 
      min-height: 38px !important;
    }
  }
}

@mixin normal_dropdown {
    @include select_style;
    .css-yk16xz-control,
    .css-1pahdxg-control {
        background: #F4F4F4;
        border-radius: 24px;
        border: 1px solid transparent !important;
        .css-g1d714-ValueContainer {
            padding: 8px 15px;
        }
    }
    .css-1pahdxg-control {
        border-color: $color_blue !important;
    }
    @media (max-width: 767px) {
      .css-yk16xz-control,
      .css-1pahdxg-control { 
        .css-g1d714-ValueContainer {
          padding: 6px 15px;
        }
      }
    }
}
