.footer-top {
    background: $color_light_black;
    img { width: auto;}
    h6 { padding: 15px 0; color: $color_white;}
    .signup-b2buser-link { 
      padding: 0;
      font-size: 14px; line-height: 19px; color: $color_white; text-transform: uppercase; letter-spacing: 1px; font-weight: $font_weight_700; display: inline-block;
      &:hover { color: #ddd !important;}
    }
    h4  { padding-bottom: 10px; color: $color_white;}
    ul {
        li{
            padding: 10px 0;
           .nav-link {
             padding: 0;
              page-break-after: auto;
              font-size: 14px;
              line-height: 22px;
              color: $color_white !important;
              font-weight: $font_weight_500;
              text-transform: capitalize;
              &:hover { text-decoration: underline;}
            }
        }
    }
    ul.double-list {
      vertical-align: top;
        li {
            display: inline-block;
            width: calc( 100% / 2 - 2px);
        }
    }
    .get-update {
        padding-top: 35px;
        p { font-size: 13px; line-height: 22px; color: $color_white;}
        .form__group {
            position: relative;
            padding: 15px 0 0;
            margin: 0;
            width: 80%;
          }
          .form__field {
            width: 100%;
            border: 0;
            border-bottom: 1px solid $color_white;
            outline: 0;
            font-size: 14px;
            color: #fff;
            padding: 2px 0;
            background: transparent;
            transition: border-color 0.2s;
            &::placeholder {
              color: transparent;
            }
            &:placeholder-shown ~ .form__label {
              font-size: 14px;
              cursor: text;
              top: 20px;
              color: $color_grey;
            }
          }
          .form__label {
            position: absolute;
            top: 0;
            display: block;
            transition: 0.2s;
            font-size: 14px;
            color: #ddd;
            text-transform: capitalize;
          }
          .form__field:focus {
            ~ .form__label {
              position: absolute;
              top: 0;
              display: block;
              transition: 0.2s;
              font-size: 10px;
              color: #fff;
              font-weight: $font_weight_700;    
            }
          }
          /* reset input */
          .form__field{
            &:required,&:invalid { box-shadow:none; }
          }
          button {
              padding: 0;
              margin-left: 25px;
              background: #2E7FFF;
              height: 38px;
              width: 38px;
              border-radius: 50px;
              border: none;
              position: relative;
              &:hover { background: $color_blue;}
              input[type="submit"]{
                  background:transparent;
              }
              &::before { content: '\f061'; font-family: 'FontAwesome'; position: absolute; top: calc(50% - 12px); left: calc(50% - 6.85px); display: block;}
          }
    }
    .social-ic{
        padding-top: 135px;
        li {
            padding-right: 15px;
            display: inline-block;
            .nav-link {
                border: 1px solid $color_grey;
                height: 35px;
                width: 35px;
                border-radius: 50px;
                display: flex;
                display: -webkit-flex;
                align-items: center;
                -webkit-align-items: center;
                justify-content: center;
                -webkit-justify-content: center;
                transition-duration: .3s;
                -webkit-transition-duration: .3s;
                &:hover {
                    text-decoration: none;
                    transform: scale(1.3);
                }
            }
        }
    }
    /* Responsive Design */
    @media (min-width: 992px) and (max-width: 1199px) {  
      .social-ic                { padding-top: 50px;}
    }
    @media (min-width: 768px) and (max-width: 991px) { 
      .social-ic                { padding-top: 35px;}
      ul.double-list {
        width: 50%;
        display: inline-block;
        vertical-align: top;
      }
      .get-update {
        padding: 8px 0 0 15px;
        width: 50%;
        display: inline-block;
      }
    }
    @media ( max-width: 767px) {
      .social-ic                { padding-top: 15px;}
      h4                        { padding: 25px 0 15px;}
      ul.double-list li {
        display: block; width: 100%;
      }
      .get-update               { padding: 0;}
    }
}
.footer-bottom { 
    padding: 15px 0;
    background: $color_light_black;
    border-top: 1px solid #878687;
    text-align: center;
    p { margin: 0; color: $color_white; font-weight: $font_weight_300;}
}