/* Slider Css */
.home-slider {
    margin-bottom: 70px;
    .slider-img {
        height: 600px;
        background-size: cover !important;
        background-repeat: no-repeat !important;
        background-position: center center !important;
    }
    .slider-content {
        height: 600px;
        width: 420px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        h1{
            color: $color_blue;
        }
        h2{
            padding: 15px 0 25px;
        }
        h4{
            font-weight: $font_weight_500;
            color: $color_grey;
        }
    }
    .slick-prev, .slick-next { 
        z-index: 1; padding: 12px; background: $color_white !important; height: 38px; width: 38px; border-radius: 50px; box-shadow: 0px 0px 12px 0 rgba(0,0,0,0.10);
        &::before { content: ''; background: url('../../images/slider-arrow.svg') no-repeat; height: 22px; width: 12px; position: absolute; top: 9px; left: 15px; display: block;}
        &:hover { 
            background: $color_blue !important;
            &::before { filter: brightness(100); -webkit-filter: brightness(100);}
        }
    }
    .slick-prev { 
        left: 20px;
        &::before { transform: rotate(-180deg); top: 7px; left: 11px;}
    }
    .slick-next { 
        right: 20px; 
    }
    .slick-dots {
        bottom: -35px;
        li {
            margin: 0;
            button{
                &::before { font-size: 10px; opacity: 1; color: #DADADA;}
            }
        }
        li.slick-active button:before {
            opacity: 1;
            color: #1A1A1A;
        }
    }                      
}

/*Trending Product Css*/
.section-title {
    padding-bottom: 35px;
    font-size: 48px;
    line-height: 58px;
    color: $color_light_black;
    text-align: center;
    @media (min-width: 1200px) and (max-width: 1499px) { 
        font-size: 40px;
        line-height: 48px;
    }
}
.view-all-btn { margin-top: 35px; text-align: center;}

/*Advertisement Banner Css*/
.advertisement-banner {
    img { height: 300px; width: 100%; object-fit: contain;}
}

/*New Arrival Sec*/
.new-arrival-img{
    padding: 15px;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    background-position: center center !important;
    height: 500px;
    border-radius: 12px;
    position: relative;
    z-index: 1;
    img { width: auto;}
    a { 
        padding-top: 10px; font-size: 12px; color: $color_white; text-transform: uppercase; font-weight: $font_weight_700; letter-spacing: 1px;
        &:hover { color: #ddd !important;}
    }
}
.new-arrival-blk{
    h1 { font-size: 48px; line-height: 58px; color: $color_white;}
    &::before { content: ''; background: rgba(0,0,0,0.40); height: 100%; width: 100%; position: absolute; top: 0; left: 0; display: block; z-index: -1; border-radius: 12px;}
}
.new-collection-blk {
    h3 { color: $color_white;}
    &::before { content: ''; background: -moz-linear-gradient(top, rgba(26,26,26,0) 0%, rgba(26,26,26,0.8) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, rgba(26,26,26,0) 0%,rgba(26,26,26,0.8) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, rgba(26,26,26,0) 0%,rgba(26,26,26,0.8) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */height: 100%; width: 100%; position: absolute; top: 0; left: 0; display: block; z-index: -1; border-radius: 12px;}
}

/*Customize Design Info Css*/
.customize-design-info-sec {
    background: #F9F9F9;
    .customize-design-info-text {
        h1 { font-size: 42px; line-height: 55px;}
        p { margin: 0; padding: 15px 0; font-size: 16px; line-height: 24px; font-weight: $font_weight_500; color: $color_grey;}
        .start-design-btn { margin-top: 35px;}
    }
}

/* Responsive */
@media (min-width: 1200px) and (max-width: 1499px) { 
    .home-slider {
        .slider-img {
            height: 550px;
        }
    }
}

@media (min-width: 992px) and (max-width: 1199px) { 
    .home-slider {
        margin-bottom: 35px;
        .slider-img {
            height: 500px;
        }
        .slider-content {
            margin-left: 35px;
            height: 500px;
            h2 { padding: 10px 0 15px; font-size: 22px; line-height: 28px;}
        }
    }
    /*Advertisement Banner Css*/
    .advertisement-banner {
        padding: 0;
    }
    /*New Arrival Sec*/
    .new-arrival-sec {
        padding-bottom: 0;
        .new-arrival-img {
            height: 380px;
        }
        .new-arrival-blk { 
            h1 { font-size: 30px; line-height: 35px;}
        }
    }
    /*Customize Design Info Css*/
    .customize-design-info-sec {
        .customize-design-info-text {
            h1 { padding: 20px 0 15px; font-size: 32px; line-height: 40px;}
            p { padding: 5px 0; font-size: 14px; line-height: 22px;}
            .start-design-btn { margin-top: 20px;}
        }
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .home-slider {
        margin-bottom: 35px;
        .slider-img {
            height: 450px;
        }
        .slider-content {
            margin-left: 35px;
            width: 450px;
            h2 { padding: 10px 0 15px; font-size: 18px; line-height: 24px;}
        }
    }
    .section-title {
        padding-bottom: 15px;
        font-size: 35px;
        line-height: 45px;
    }
    /*Advertisement Banner Css*/
    .advertisement-banner {
        padding: 0;
        img { height: 100%; width: 100%; object-fit: cover;}
    }
    /*New Arrival Sec*/
    .new-arrival-sec {
        padding-bottom: 0;
        .new-arrival-img {
            height: 380px;
            margin-bottom: 30px;
        }
        .new-arrival-blk { 
            h1 { font-size: 30px; line-height: 35px;}
        }
    }
    .view-all-btn { margin-top: 25px;}
    /*Customize Design Info Css*/
    .customize-design-info-sec {
        .customize-design-info-text {
            h1 { padding: 20px 0 15px; font-size: 24px; line-height: 30px;}
            p { padding: 5px 0; font-size: 14px; line-height: 22px;}
            .start-design-btn { margin-top: 20px;}
        }
    }
}

@media (max-width: 767px) { 
    .home-slider{
        margin-bottom: 35px;
        .slider-img {
            height: 400px;
        }
        .slider-content {
            width: 100%;
            height: 400px;
            h2 { padding: 10px 0 15px; font-size: 18px; line-height: 24px;}
        }
        .slick-arrow { display: none !important;}
    }
    .section-title {
        padding-bottom: 15px;
        font-size: 25px;
        line-height: 32px;
    }
    /*Advertisement Banner Css*/
    .advertisement-banner {
        padding: 0;
        img { height: 100%; width: 100%; object-fit: cover;}
    }
    /*New Arrival Sec*/
    .new-arrival-sec {
        padding-bottom: 0;
        .new-arrival-img {
            height: 350px;
            margin-bottom: 15px;
        }
        .new-arrival-blk { 
            h1 { font-size: 30px; line-height: 35px;}
        }
    }
    .view-all-btn { margin-top: 25px;}
    /*Customize Design Info Css*/
    .customize-design-info-sec {
        .customize-design-info-text {
            h1 { padding: 20px 0 15px; font-size: 24px; line-height: 30px; text-align: center;}
            p { padding: 5px 0; font-size: 14px; line-height: 22px; text-align: center;;}
            .start-design-btn { margin-top: 20px; text-align: center;}
        }
    }
}