.pricing-table {
    .artist-section-title p { 
        margin: 0; font-weight: $font_weight_600;
        span { font-weight: $font_weight_500; color: $color-grey;}
    }
}
.table-style {
    width: 100%;
    img { width: auto;}
    th { padding: 12px 15px; border-top: 1px solid #dadada; border-bottom: 1px solid #dadada;; font-size: 12px; line-height: 19px; font-weight: $font_weight_600; color: $color-grey; text-transform: uppercase;}
    tr { border-bottom: 1px solid #f4f4f4;}
    td { padding: 12px 15px; font-weight: $font_weight_600; }
    .form-control { padding: 5px 15px; width: 98px; height: 34px; text-align: center;}
    .btn-outline {
        margin-left: 25px;
        padding: 6px 15px;
        width: 98px;
        font-weight: $font_weight_600;
        &.edit {
            border-color: #dadada;
            color: $color-blue;
        }
        &.save {
            border-color: $color-green !important;
            color: $color-green;
            &:hover {
                background: $color-green;
            }
        }
    }
    .checkbox-style [type="checkbox"]:not(:checked) + label:before, 
    .checkbox-style [type="checkbox"]:checked + label:before { 
        top: -14px;
        border-radius: 4px;
        height: 20px;
        width: 20px;
    }
    .checkbox-style [type="checkbox"]:not(:checked) + label:after, 
    .checkbox-style [type="checkbox"]:checked + label:after {
        top: -12px;
        left: 4px;
    }
    .checkbox-style [type="checkbox"]:checked + label::before {
        background: $color-white;
        border: 1px solid $color-red;
    }
    .checkbox-style [type="checkbox"]:not(:checked) + label:after, 
    .checkbox-style [type="checkbox"]:checked + label:after {
        color: $color-red;
    }
    .paid, .delivered { color: $color-green;}
    .pending { color: #E8A500;}
    .cancel { color: $color-red;}
}