.my-order-sec {
  h2 {
    font-size: 48px;
    line-height: 55px;
  }
  .order-tab {
    padding-bottom: 15px;
    border-bottom: 1px solid #b9b9b9;
    position: relative;
    .nav-tabs{
      border: none;
      position: absolute;
      right: 0;
      bottom: 0;
      .nav-item {
        .nav-link {
          padding: 5px 18px;
          border: none;
          color: $color_light_black;
          font-weight: $font_weight_700;
          min-width: 140px;
          text-align: center;
          cursor: pointer;
          &.active, &:hover {
            border: none;
            border-bottom: 2px solid $color_red;
            color: $color_red !important;
          }
        }
      }
    }
  }
  @media (min-width: 1200px) and (max-width: 1499px) { 
    h2 { font-size: 40px; line-height: 48px;}
  }
  @media (min-width: 768px) and (max-width: 991px) { 
    h2 { font-size: 25px; line-height: 32px;} 
  }
  @media (max-width: 767px) { 
    h2 { padding-bottom: 10px; font-size: 18px; line-height: 24px;} 
    .order-tab {
      display: block;
      padding-bottom: 35px;
      margin-bottom: 20px;
      .nav-tabs {
        left: 0;
        width: 100%;
        .nav-item {
          width: calc(100% / 2);
          .nav-link {
            background: transparent;
          }
        }
      }
    }
  }
}
/* Order Cards */
.card {
  margin: 18px 0;
  padding: 15px 15px 25px;
  img { width: auto;}
  h6 { font-weight: $font_weight_600;}
  li { 
    margin-bottom: 5px;
    padding-right: 25px;
    display: inline-block;
    font-size: 14px;
    line-height: 19px;
    color: $color_grey;
    font-weight: $font_weight_500;
    text-transform: capitalize;
    letter-spacing: 1px;
    span{
      font-weight: $font_weight_600;
      color: $color_light_black;
    }
  }
  /* Order Info */
  .order-info{
    padding-bottom: 15px;
    border-bottom: 1px solid #dadada;
    .btn-outline {
      margin-left: 15px;
      padding: 8px 25px;
      font-size: 12px;
      line-height: 19px;
      font-weight: $font_weight_600;
      flex: 0 1 175px;
    }
  }
  /* Order List */
  .orderd-product-list {
    padding: 15px;
    border-bottom: 1px solid #f4f4f4;
    .product-img {
      margin-right: 15px;
      height: 70px;
      width: 70px;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      background-position: center center !important;
      flex-shrink: 0; -webkit-flex-shrink: 0;
    }
    h5 { font-weight: $font_weight_600;}
    li{
      padding-left: 50px;
      padding-right: 0;
      span { margin-top: 5px; display: block;}
    }
    a {
      padding-top: 15px;
      float: right;
      font-size: 10px;
      line-height: 15px;
      font-weight: $font_weight_500;
      color: $color_blue;
      &:hover { text-decoration: underline;}
    }
  }
  /* Pending Status */
  .pending-status {
    padding-top: 15px;
    h5 {
       font-weight: $font_weight_700; color: #FF9F49; text-transform: uppercase; letter-spacing: 1px;
       img { margin-right: 8px;}
      }
  }
  /* Total Tool Tip */
  .tooltip-box {
    position: relative;
    a { 
      padding-right: 10px; font-size: 20px; color: #b9b9b9;
    }
    .price-summery {
      display: none;
      padding: 15px;
      width: 250px;
      background: $color_white;
      box-shadow: 6px 6px 30px rgba(0,0,0,0.08);
      border-radius: 10px;
      position: absolute;
      bottom: 35px;
      right: -10px;
      h4 { font-size: 12px;}
      li {
        padding: 5px 0;
        display: flex;
        display: -webkit-flex;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        font-size: 12px;
        color: $color_light_black;
      }
      .total-pay { 
        margin-top: 10px;
        padding: 10px 0 18px;
      }
      &::before { bottom: -18px; right: 18px; border: solid transparent; content: " "; height: 0; width: 0; position: absolute; pointer-events: none; border-top-color: $color_white; border-width: 10px;}
    }
    &:hover .price-summery { display: block;}
  }
  /* Responsive Css */
  @media (min-width: 1200px) and (max-width: 1499px) { 
    padding: 12px 15px;
    margin-top: 0;
    .price-summery {
      .total-pay {
        padding: 10px 0;
      }
    }
    .order-info {
      padding-bottom: 10px;
    }
  }
  @media (min-width: 992px) and (max-width: 1199px) { 
    padding: 15px;
    /* Order Info */
    .order-info {
      li {
        span { display: block;}
      }
    }
    /* Order List */
    .orderd-product-list { 
      h5 { padding: 15px 0;}
      li { padding-left: 45px;}
    }
  }
  @media (min-width: 768px) and (max-width: 991px) { 
    padding: 15px;
    /* Order Info */
    .order-info {
      li {
        font-size: 12px;
        line-height: 17px;
        span { display: block;}
      }
    }
    /* Order List */
    .orderd-product-list { 
      h5 { padding: 5px 0;}
      li { padding-left: 30px;}
    }
    /* Total Tool Tip */ 
    .tooltip-show {
      position: relative;
      .price-summery {
        display: block !important;
      }
    }
    .tooltip-box {
      &:hover .price-summery { display: none;}
    }
  }
  @media ( max-width: 767px) {
    margin: 0 0 18px;
    padding: 15px;
    /* Order Info */
    .order-info {
      display: block;
      li {
        text-align: left;
        margin-bottom: 10px;
        padding: 0;
        width: calc( 100% / 2);
        min-width: 100px;
        font-size: 12px;
        line-height: 17px;
        span { display: block;}
      }
      .btn-outline {
        margin: 10px 0 5px;
      }
    }
    /* Order List */
    .orderd-product-list { 
      padding: 15px 0;
      text-align: center;
      display: block;
      .align-center { display: block;}
      .product-img {
        margin: 0 auto;
      }
      h5 { padding: 15px 0;}
      li { 
        padding-left: 5px;
        width: calc( 100% / 4 ) ;
      }
    }
    /* Pending Status */
    .pending-status {
      display: block;
      text-align: center;
      .align-center { justify-content: center; -webkit-justify-content: center;}
      h5 { padding-bottom: 15px;}
    }
    /* Total Tool Tip */ 
    .tooltip-show {
      position: relative;
      .price-summery {
        display: block !important;
        left: 50%;
        right: auto;
        transform: translateX(-50%);
        -webkit-transform: translateX(-50%);
        &::before { left: 48px;}
      }
    }
    .tooltip-box {
      position: unset;
      &:hover .price-summery { display: none;}
    }
  }
}
/*============================================================*/
/* More Details Css*/
.more-details-sec {
  .pending-status {
    padding-top: 0px;
  }
}
.cancel-order{
  padding: 15px 0;
  background: $color_light_black;
  a{ color: $color_white !important;}
  h5{
    padding-left: 15px;
    color: #DADADA;
    span {font-weight: $font_weight_600; color: $color_white;}
  }
  .btn-outline {
    margin-left: 15px;
    padding: 10px 25px;
    border-color: $color_grey;
    color: $color_white;
  }
  @media ( max-width: 767px) {
    .justify-space-between {
      display: block;
    }
    .btn-outline {
      margin: 10px auto 0;
      display: block;
    }
  }
}
.more-details-card {
  .address-info {
    padding: 0;
    padding-top: 25px;
    h5 a { color: $color_light_black; font-weight: $font_weight_600;}
  }
  .total-info {
    display: flex; display: -webkit-flex; flex-direction: column; -webkit-flex-direction: column; align-items: flex-end; -webkit-align-items: flex-end;
    h5 { padding-bottom: 8px; color: $color_light_black; text-align: right;}
  }
  .btn-text { 
    float: right;
    padding: 20px 0 0;
    font-size: 14px;
    line-height: 19px;
    color: $color_blue !important;
  }
  @media ( min-width: 768px) and ( max-width: 991px) {
    .total-info {
      margin-top: 15px;
    }
  }
  @media ( max-width: 767px) {
    .card {
      .pending-status h5 {
        padding: 15px 0 0;
      }
      .address-info {
        h5                                            { margin: 0; padding-bottom: 10px;}
        p.small-font                                  { margin: 0 0 10px;}
        .col-md-6 { 
          margin-bottom: 10px; border: none; border-bottom: 1px solid #d8d8d8;                 
        }
      }
      .orderd-product-list a {
        float: none;
      }
      .btn-text {
        font-size: 12px;
        line-height: 19px;
        display: inherit;
        float: none;
      }
      .total-info {
        align-items: center; -webkit-align-items: center;
      }
    }
  }
}
/* Rating Modal Css*/
.rating-modal {
  h6 {
    font-size: 16px;
    font-weight: $font_weight_700;
    letter-spacing: 1px;
    text-transform: uppercase;
  }
  @media ( max-width: 767px) {
    h6 {
      font-size: 12px;
    }
    .rating:not(:checked) > label { 
      font-size: 20px;
      width: 20px;
      margin: 0 8px 0 0;
    }
  }
}
.rating {
  margin-right: 15px;
  float:left;
}
.rating:not(:checked) > input {
  position:absolute;
  top:-9999px;
  clip:rect(0,0,0,0);
}
.rating:not(:checked) > label {
  float:right;
  width: 24px;
  padding: 0;
  margin: 0 12px 0 0;
  overflow:hidden;
  white-space:nowrap;
  cursor:pointer;
  font-size: 24px;
  line-height:28px;
  color:#dadada;
  // @media ( max-width: 767px) {
  //   font-size: 20px;
  //   width: 20px;
  //   margin: 0 8px 0 0;
  // }
}
.rating:not(:checked) > label:before {
  content: '\f005 ';
  font-family: 'FontAwesome';
}
.rating > input:checked ~ label {
  color: #FF654F;
}
.rating:not(:checked) > label:hover {
  color: #FF654F;
}
.rating > label:active {
  position:relative;
  top:2px;
  left:2px;
}
/* Return Order */
input[type="file"] { display: none;}  
.add-img {
  margin: 10px 0 25px;
  .box {
    margin: 0px 15px 7px 0;
    border-radius: 12px;
    position: relative;
    width: calc(100% / 5 - 15px);
    display: inline-block;
    vertical-align: top;
    label { 
      margin: 0;
      .btn.delete-ic { 
        padding: 0; position: absolute; top: -15px; right: -5px; background: transparent; border: none;
        &::before { content: "\f057"; color: $color_red; text-align: center; font-size: 20px; display: block; font-family: 'FontAwesome'; border-radius: 50%;}
      }
    }
    .add-img-blk {
      padding: 10px 12px;
      border: 2px dashed #dadada;
      text-align: center;
      border-radius: 12px;
      cursor: pointer;
      img { width: auto;}
      p { margin: 0; padding-top: 15px; color: #b9b9b9; font-weight: $font_weight_500; text-transform: capitalize;}
    }
    .return-img {
      height: 95px;
      width: 95px;
      background-size: cover !important;
      background-repeat: no-repeat !important;
      background-position: center center !important;
      border-radius: 12px;
    }
  }
  @media ( max-width: 767px) {
    display: block !important;
    margin-bottom: 15px;
    .box{
      width: calc(100% / 3 - 15px);
      .return-img {
        height: 70px;
        width: 70px;
      }
      .add-img-blk {
        padding: 7px 12px;
        img { width: 18px;}
        p { padding-top: 7px; font-size: 10px; line-height: 12px;}
      }
      label{
        .btn.delete-ic { 
          top: -16px;
          right: -8px;
        }
      }
    }
  }
} 