.accordion-blks {
  .accordion {
    border: none;
    .accordion__item { margin-bottom: 15px; border: 1px solid #dadada; border-radius: 10px;} 
    .accordion__button {
      padding: 15px;
      background: $color_white;
      border: none;
      border-radius: 10px;
      position: relative;
      &:focus { outline: none;}
      &::before { margin-right: 0; position: absolute; border-color: #000; right: 15px; top: 50%; transition-duration: .3s; transform: translateY(-50%) rotate(45deg); -webkit-transform: translateY(-50%) rotate(45deg);}
      &[aria-expanded='true']::before, &[aria-selected='true']::before { transform: translateY(-50%) rotate(225deg); -webkit-transform: translateY(-50%) rotate(225deg);}
    }
    .review-product-blk {
      .tee-img { 
        margin-right: 15px;
        height: 80px;
        width: 80px;
        background-size: cover !important;
        background-position: center center !important;
        background-repeat: no-repeat !important;
      }
      h6 { padding-bottom: 10px; font-weight: $font_weight_600;}
      h5 { 
        padding-bottom: 5px; font-size: 12px; line-height: 19px; color: $color_grey;
        span { font-weight: $font_weight_600; color: $color_light_black;}
      }
    }
    .reply-review-blk {
      padding-left: 90px;
      .reply-blk {
        margin-top: 15px;
        .review-user-img {
          margin-right: 10px;
          height: 20px;
          width: 20px;
        }
        h5 { color: $color_grey; font-weight: $font_weight_500;}
        p { margin: 0;}
      }
    }
  }
}