.artist-section-title {
  margin-bottom: 35px;
  h2 { font-size: 48px; line-height: 55px;}
  @media (min-width: 1200px) and (max-width: 1499px) { 
    margin-bottom: 25px;
    h2 { font-size: 40px; line-height: 48px;}
  }
}
/* Dashboard Content */
.dashboard-content {
  .btn-1 {
    background: $color_light-black;
    &:hover { background: $color_blue;}
    i { padding-right: 10px;}
  }
  .dashboard-details-card {
    .card {
      position: relative;
      margin: 0;
      box-shadow: none;
      border-radius: 24px;
      overflow: hidden;
      img { width: 22px;}
      &::before{
        content: '';
        height: 50px;
        width: 100%;
        border-radius: 50%;
        position: absolute;
        bottom: -35px;
        left: 0;
      }
      &.card-earning {
        background: #EDE3F2;
        &::before {
          background: #D8B6E9;
        }
      }
      &.total-items {
        background: #CEF0F3;
        &::before {
          background: #8CE6EE;
        }
      }
      &.today-earn {
        background: #DBDCF0;
        &::before {
          background: #CED0FD;
        }
      }
      &.today-item-sold {
        background: #F0E6C1;
        &::before {
          background: #E9DBA5;
        }
      }
      .card-ic {
        margin-right: 15px;
        padding: 7px 9px;
        height: 40px;
        width: 40px;
        border-radius: 8px;
        background: $color_light-black;
      }
      h6 { font-weight: $font_weight_500;}
      h2 { padding: 15px 0;}
    }
  }
}
/* Chart Css */
.chart-sec {
  margin-top: 35px;
  padding: 20px 15px;
  border: 1px solid #B9B9B9;
  border-radius: 24px;
  .chart-filter {
    label {
      margin: 0 10px 0 0;
      font-size: 14px;
      line-height: 19px;
      color: $color_grey;
      letter-spacing: 0;
      text-transform: capitalize;
    }
    .select-year {
      width: 130px;
    }
    .normal-dropdown {
      width: 140px;
      .css-yk16xz-control .css-g1d714-ValueContainer, 
      .css-1pahdxg-control .css-g1d714-ValueContainer {
        padding: 5px 15px;
      }
      .css-yk16xz-control, .css-1pahdxg-control {
        min-height: 35px !important;
      }
    }
    ul {
      li { 
        margin-right: 25px;
        display: inline-block;
        &:last-child { margin: 0;}
      }
    }
  }
  .product-result {
    border-left: 1px solid #b9b9b9;
    h4 { padding-bottom: 10px; font-weight: $font_weight_500; text-align: center;}
    .earning-sold-item {
      margin: 15px 0;
      padding: 10px 15px;
      background: #f9f9f9;
      border: 1px solid #dadada;
      border-radius: 6px;
      h4 { padding: 0; margin-left: 10px; font-weight: $font_weight_600;}
    }
  }
  .chart-tab {
    .nav-tabs {
      margin-bottom: 12px;
      display: flex;
      display: -webkit-flex;
      justify-content: flex-end;
      -webkit-justify-content: flex-end;
      border: none;
      .nav-item {
        margin-right: 0;
        .nav-link {
          padding: 0 20px 0 0;
          border: none;
          cursor: pointer;
          font-size: 14px;
          line-height: 22px;
          font-weight: $font_weight_700;
          color: $color_grey;
          letter-spacing: 1px;
          text-transform: uppercase;
          &:hover, &.active { color: $color_red !important;}
        }
      }
    }
  }
}
/* Top Products */
.top-products {
  background: #f9f9f9;
}
/* Date Range Picker */
.date-range-picker {
  width: 400px;
  .form-control {
    padding: 0;
    .date-range-input {
      position: relative;
      input {
        padding: 10px 40px 10px 15px;
        background: transparent;
        border: none;
      }
      &::before { content: ''; background: url('../../../images/daterange-ic.svg') no-repeat; height: 20px; width: 20px; position: absolute; top: 10px; right: 15px; display: block;}
      &::after  { content:''; background: #979797; height: 22px; width: 1px; position: absolute; top: 10px; right: 0;}
      &:last-child::after { display: none;}
    }
  }
}
.date-range-style {
  .popover {
    max-width: 100%;
    border: none;
    box-shadow: 0px 0px 8px #ddd;
    border-radius: 4px;
    .arrow{
      &::before, &::after { display: none;}
    }
    .rdrDateRangePickerWrapper {
      .rdrDefinedRangesWrapper { 
        display: none;
      }
      .rdrCalendarWrapper {
        border-radius: 4px;
        .rdrDateDisplayItemActive {
          border-color: $color_blue;
        }
        .rdrMonthAndYearWrapper {
          .rdrMonthAndYearPickers select {
            padding: 8px 30px 8px 10px;
            &:hover { background: #f4f4f4;}
          }
        }
        .rdrMonth {
          .rdrDay {
            .rdrSelected, 
            .rdrInRange, 
            .rdrStartEdge, 
            .rdrEndEdge {
              background: $color_blue;
            }
          }
        }
      }
    }
  }
}