.product-listing {
    .col        { width: calc(100% / 4); display: inline-flex; min-width: auto; flex-basis: calc(100% / 4); -webkit-flex-basis: calc(100% / 4); flex-grow: 0; -webkit-flex-grow: 0;}
    @media (min-width: 1200px) and (max-width: 1599px)
    {
      .col        { width: calc(100% / 3); flex-basis: calc(100% / 3); -webkit-flex-basis: calc(100% / 3);}
    }
}
.products-blk {
    margin: 15px 0;
    background: #fff;
    border: 1px solid #F4F4F4;
    border-radius: 8px;
    cursor: pointer;
    transition-duration: .3s;
    -webkit-transition-duration: .3s;
    &:hover {
        box-shadow: 8px 10px 14px rgba(0,0,0,0.06);
        .product-img .tshirt-img{
            transform: scale(1.1); -webkit-transform: scale(1.1);
        }
    }
    .product-img {
        padding: 20px;
        border-radius: 8px 8px 0 0;
        overflow: hidden;
        position: relative;
        .tshirt-img {
            height: 250px;
            width: 100%;
            object-fit: contain;
            transition: all 1.2s; 
            -webkit-transition: all 1.2s;
        }
        .add-wishlist {
            padding: 3px 5px;
            background: #fff;
            height: 32px;
            width: 32px;
            border: none;
            border-radius: 50px;
            box-shadow: 0px 2px 4px rgba(0,0,0,0.10);
            display: inline-block;
            position: absolute;
            top: 10px;
            right: 10px;
            img { 
                object-fit: none; object-position: 0 0px; width: 20px; height: 18px;
            }
            &:hover img {
                object-position: 0 -18px;
            }
        }
    }
    .product-info{
        padding: 12px;
        a { font-size: 12px; line-height: 15px; font-weight: $font_weight_500; color: $color_grey; display: inline-block; text-decoration: underline;}
        h6{
            padding: 12px 0 22px;
            a { font-size: 16px; line-height: 24px; color: $color_light_black; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical;  overflow: hidden; text-transform: capitalize; text-decoration: none;}
        } 
    }
    .product-price-color {
        .price { margin-right: 10px; font-size: 22px; line-height: 28px; color: $color_light_black; font-weight: $font_weight_600;}
        .old-price { display: none; padding: 0px 5px; font-size: 15px; line-height: 19px; color: #b9b9b9; font-weight: $font_weight_500; text-decoration: line-through;}
        .product-discount { 
            font-size: 12px; line-height: 16px; color: $color_green; font-weight: $font_weight_500;
            span { font-size: 10px; line-height: 15px; display: block;}
        }
    }
    @media (min-width: 1200px) and (max-width: 1499px) {
        .product-info {
            h6 {
                a { font-size: 14px; line-height: 22px;}
            }
        }
    }
    @media ( max-width: 767px) {
        .product-info h6 a { font-size: 14px; line-height: 22px;}
    }
}
/* More Color */
.product-colors-blk {
    position: relative;
    .more-color-sign {
        position: absolute;
        top: -2px; 
        left: 0;
    }
    ul{
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        li {
            padding: 2px 0;
            display: inline-block;
            width: calc(100% / 2 - 5px);
        }
        .product-colors { height: 8px; width: 8px; border-radius: 50px;}
    }
}