.enter-otp-blk { 
  h4 { font-size: 18px;}
  h6 { 
    span { font-weight: $font_weight_600;}
  }
  .edit-number { padding-left: 5px;}
  .otp-time {
    text-align: center;
    span { font-size: 14px; line-height: 19px; color: $color_red; font-weight: $font_weight_600;}
  }
  .resend-code { 
    p {
        margin: 25px 0 0;
        text-align: center; 
    }
    .btn-text {
      padding: 0;
      color: $color_blue !important;
    }
  }
  @media ( min-width: 1200px) and (max-width: 1499px) { 
    h4 { font-size: 16px;}
  }
  @media ( max-width: 767px) {
    h4 { font-size: 14px; text-align: center;}
  }
}

