.checkout-sec {
  h2 { padding-bottom: 25px;}
  /***** Order Summery *****/
  .order-summery{
    h5 {
      padding-top: 10px;
      font-size: 13px;
      color: $color_blue;
      span { font-weight: $font_weight_600;}
    }
    .checkout-product-list {
      border-bottom: 1px solid #f4f4f4;
      padding: 12px;
      .checkout-pro-img {
        margin-right: 15px;
        height: 60px;
        width: 60px;
        background-size: cover !important;
        background-position: center center;
        background-repeat: no-repeat !important;
        flex-shrink: 0; -webkit-flex-shrink: 0;
      }
      h6 a {
        padding-bottom: 5px;
        font-size: 12px;
        line-height: 19px;
        color: $color_light_black;
        font-weight: $font_weight_500;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;  
        overflow: hidden;
      }
      ul{
        li { 
          padding-right: 15px; display: inline-block; font-size: 12px; color: $color_grey; font-weight: $font_weight_500;
          span{ color: $color_light_black; font-weight: $font_weight_600;}
        }
      }
      .checkout-pro-price {
        margin-left: 15px;
        .small-font { font-weight: $font_weight_600;}
      }
      .multiple-quantities{
        ul:not(:last-child){
          margin-bottom: 8px;
        }
      }
    }
    .price-summery { margin-bottom: 15px; padding-top: 10px;}
    .rcs-custom-scroll {
      .panel-content-custom {
        max-height: 270px;
        width: 100%;
      }
    }
  }
  /***** Payment Option *****/
  .payment-select {
    padding-top: 25px;
    .payment-option-blk {
      margin-top: 20px;
      border: 1px solid #dadada;
      padding: 12px 15px 8px;
      border-radius: 10px;
      img { margin-left: 5px; width: auto;}
    }
  }
  .btn-1,
  .btn-outline { width: 100%;}

  @media (min-width: 1200px) and (max-width: 1499px) { 
    h2 { padding-bottom: 25px;}
    .payment-select { padding-top: 18px;}
  }
  @media ( min-width: 992px) and ( max-width: 1199px) { 
    .btn-1,
    .btn-outline { padding: 12px 25px; font-size: 12px; max-height: 40px;}
  }
  @media ( min-width: 768px) and ( max-width: 991px) {
    .pay-btn { 
      text-align: center;
      .btn-1 { width: 50%;}
    }
    h2 { padding-bottom: 8px;}
    .payment-select {
      .payment-option-blk {
        margin: 15px 15px 0 0;
        width: calc( 100% / 2 - 15px);
        display: inline-block;
      }
    }
  }
  @media ( max-width: 767px) {
    h2 { padding-bottom: 15px;}
    .btn-1,
    .btn-outline { padding: 12px 25px; font-size: 12px; max-height: 40px;}
    .btn-1       { margin-bottom: 15px;}
    .payment-select {
      .payment-option-blk {
        padding: 8px 15px 5px;
      }
    }
    /***** Order Summery *****/
    .order-summery {
      .card { margin: 18px 0 0;}
      .checkout-product-list {
        display: block !important;
        text-align: center;
        .checkout-pro-img {
          margin: 0 auto 15px;
        }
        .checkout-pro-price {
          margin: 15px 0;
          p {
            margin: 0;
            font-size: 18px;
          }
        }
      }
      .price-summery {
        margin-bottom: 0;
        li { 
          margin-bottom: 0;
          font-size: 12px;
          line-height: 19px;
        }
      }
    }
  }
}
/* Address Info */
.add-new-address {
  .add-new-btn{
    background: $color_white;
    color: $color_blue;
    border-radius: 10px;
    box-shadow: 6px 6px 30px rgba(0,0,0,0.08);
    width: 100%;
    &:hover,&:focus {
      background: $color_blue;
      color: $color_white;
    }
  }
  .office-type-select {
    label { font-weight: $font_weight_500 !important;}
  }
  .card{
    padding: 18px;
    .card-body { padding: 0;}
  }
  .btn-outline  {
    border-color: $color_blue;
    color: $color_blue;
  }
}
.card{
  .address-tag {
    margin-right: 15px;
    background: #F0F6FF;
    padding: 5px 12px;
    border-radius: 12px;
    p.small-font { margin: 0; line-height: 15px; font-weight: $font_weight_600; color: $color_blue;}
  }
  .address-contact a { color: $color_light_black; font-weight: $font_weight_600;}
  .checkbox-style {
    margin-top: 10px;
    margin-left: 15px;
    label {
      color: $color_light_black;
    }
    [type="checkbox"]:not(:checked) + label:before,
    [type="checkbox"]:checked + label:before {
      height: 20px;
      width: 20px;
    }
    [type="checkbox"]:not(:checked) + label:after,
    [type="checkbox"]:checked + label:after {
      top: 2px;
      left: 4px;
    }
  }
  @media ( max-width: 767px) {
    .checkbox-style { 
      margin-left: 0;
    }
    .card-text {
      > .align-center { flex-direction: column; -webkit-flex-direction: column;}
      .address-tag {
        margin: 10px 0;
        display: inline-block;
      }
      .address-contact {
        a         { font-size: 13px; line-height: 16px;}
      }
    }
  }
}
.address-info {
  padding: 15px 0 0 32px;
  .col-md-6 { 
    &:first-child { border: none;}
    border-left: 1px solid #d8d8d8;
  }
  h5 {
    font-size: 12px;
    color: $color_grey;
    span { font-weight: $font_weight_600; color: $color_light_black;}
  }
  p.small-font { margin: 8px 0;}
  @media ( max-width: 767px) {
    padding: 15px 0 0;
    p.small-font                                  { margin: 10px 0;}
    .col-md-6 { 
    padding-top: 10px; margin-bottom: 10px; border: none; border-top: 1px solid #d8d8d8;    
    &:first-child                                 { padding-top: 0;}                      
    }
  }
}
/***** Readio Style 2 *****/
.radio-style-2{
  [type="radio"]:checked,
  [type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px;
  }
  [type="radio"]:checked + label,
  [type="radio"]:not(:checked) + label
  {
    margin: 0;
    position: relative;
    padding: 0 20px 0 32px;
    cursor: pointer;
    font-size: 16px;
    line-height: 22px;
    display: inline-block;
    font-weight: $font_weight_600;
    text-transform: capitalize;
    letter-spacing: 0;
  }
  [type="radio"]:checked + label:before,
  [type="radio"]:not(:checked) + label:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 20px;
      height: 20px;
      border-radius: 100%;
      background: #fff;
  }
  [type="radio"] + label:before { border: 1.5px solid #dadada;}
  [type="radio"]:checked + label:before { border-color: $color_blue;}
  [type="radio"]:checked + label:after,
  [type="radio"]:not(:checked) + label:after {
      content: '';
      width: 12px;
      height: 12px;
      background: $color_blue;
      position: absolute;
      top: 4px;
      left: 4px;
      border-radius: 100%;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
  }
  [type="radio"]:not(:checked) + label:after {
      opacity: 0;
      -webkit-transform: scale(0);
      transform: scale(0);
  }
  [type="radio"]:checked + label:after {
      opacity: 1;
      -webkit-transform: scale(1);
      transform: scale(1);
  }
  @media ( max-width: 767px) {
    [type="radio"]:checked + label, 
    [type="radio"]:not(:checked) + label {
      font-size: 14px;
    }
  }
}
.autocode-pno {
  position: relative;
  .form-control { padding-left: 60px;}
  span{
    padding-right: 10px;
    position: absolute;
    top: 40px;
    left: 15px;
    border-right: 1px solid #b9b9b9;
  }
  @media ( max-width: 767px) {
    span {
      top: 38px;
    }
  }
}
.same-as-shipping-checkbox {
  .checkbox-style {
    margin: 35px 0;
  }
  @media ( max-width: 767px) {
    .checkbox-style {
      margin: 25px 0;
    }
  }
}
/*Gift Modal*/
.modal-center{
  margin: 15px auto;
  max-width: 600px;
  top: 50%;
  transform: translateY(-50%) !important;
  -webkit-transform: translateY(-50%) !important;
  .modal-content {
    border-radius: 10px;
    .modal-header { 
      padding-bottom: 0;
      border: none;
      .close{
        span {
          display: none;
        } 
        &::before { content: ''; background: url('../../images/close-ic2.svg') no-repeat; height: 20px; width: 20px; position: absolute; top: 20px; right: 15px;}
      }
    }
    .modal-body {
      padding: 15px;
      img { width: auto;}
      .modal-submit-btn {
        text-align: center;
        .btn {
          padding: 15px 60px;
        }
      }
    }
  }
  &.continue-shoping-modal {
    max-width: 450px;
    text-align: center;
    .modal-body {
      padding: 35px 35px 15px;
    }
    img { margin: 15px 0;}
    h5{
      padding: 10px 0 35px;
      font-weight: $font_weight_700;
      color: #dadada;
      text-transform: uppercase;
      letter-spacing: 1px;
      span{ color: $color_light_black;}
    }
    .btn { width: 100%;}
    .btn-text { padding-bottom: 0; color: $color_blue !important;}
  }
  .form-control { border-radius: 10px !important;}
  
  /* Responsive */
  @media (max-width: 767px) {
    margin: 15px;
    .modal-content { 
      .modal-body {
        padding: 25px 15px;
        .modal-submit-btn {
          text-align: center;
          .btn {
            padding: 12px 35px;
          }
        }
        img { width: 100px;}
      }
    }
    &.continue-shoping-modal { 
      h5 { padding-bottom: 20px;}
    }
  }
}
.card {
  margin-bottom: 18px;
  border: none;
  border-radius: 10px;
  background: $color_white;
  box-shadow: 6px 6px 30px rgba(0,0,0,0.08);
  .card-body {
    padding: 0;
  }
}