.my-account-blks {
  h2 { margin-bottom: 35px;}
  .card { margin: 0;}
  /* Account Links Css */
  .account-links-blk {
    .account-item-list {
      .card{
        padding: 0;
        .card-body {
          li {
            padding: 12px 0 6px;
            display: block;
            a {
              padding-left: 25px; 
              font-size: 16px;
              line-height: 22px;
              font-weight: $font_weight_600;
              color: $color_light_black;
              position: relative;
              &:hover, &.active{ 
                  color: $color_blue;
                  &::before{ content: ''; background: $color_blue; height: 16px; width: 8px; position: absolute; top: 50%; left: 0; transform: translateY(-50%); -webkit-transform: translateY(-50%);}
              }
            }
          }
        }
      }
    }
  }
  .my-account-content{
    h3 { padding-bottom: 25px;}
    .title-flex{
      display: flex;
      justify-content: space-between;
      a{
        text-transform: uppercase;
        font-size: 14px;
        line-height: 18px;
        font-weight: $font_weight_700;
        text-transform: uppercase;
        &:hover{
          color: $color_red !important;
        }
      }
    }
  }

  /* Profile Information Css */
  .user-profile-details {
    margin-bottom: 25px;
    .user-img {
      margin-right: 25px;
      height: 149px;
      width: 149px;
      border-radius: 50%;
      background-size: cover !important;
      background-repeat: no-repeat !important;
      background-position: center center !important;
      position: relative;
      .box label {
        position: absolute;
        bottom: 0;
        right: 0;
        cursor: pointer;
      }
    }
    &.sub-user{
      .user-img{
        height: 80px;
        width: 80px;
      }
      label img{
        width: 20px;
      }
    }
    h3 { padding: 0; font-weight: $font_weight_700;}
    h6 { padding: 10px 0; color: #B9B9B9; font-weight: $font_weight_500;}
    a { 
      color: $color_blue; font-weight: $font_weight_700; text-transform: uppercase; font-size: 14px; line-height: 18px;
      &:hover { text-decoration: underline;}
    }
    .personal-info_detail {
        flex: 1 0 auto;
        .package-name {
          margin: 0;
          text-align: right;
          img{
            margin-right: 12px;
          }
        }
        .profile-actions{
          margin-top: 10px;
          a:first-child {
              margin-right: 40px;
          }
        }
    }
  }
  .form-field {
    .action-btns{
      margin: 0 -15px;
    }
    .btn {
      margin: 15px;
      min-width: 217px;
      &.btn-outline {
        border-color: $color_blue;
        color: $color_blue;
      }
    }
    h4{
      margin-bottom: 24px;
    }
    .professional-details,.address{
      margin-top: 20px;
    }
    &.add-new-sub-user{
      .add-img{
        .box{
          height: 150px;
          width: 150px;
          border-radius: 50%;
        }
        .box.blank{
          background-color: $color_light;
          cursor: pointer;
        }
        .add-img-blk {
          padding: 34px 20px;
          border: none;
          text-align: center;
          border-radius: 0;
          cursor: pointer;
        }
        .box:not(.blank){
          margin: 0;
          .return-img{
            height: 100%;
            width: 100%;
            border-radius: 50%;
            .delete-ic{
              min-width: auto;
            }
          }
        }
      }
    }
  }
  .unfollowing-artist {
    border-top: 1px solid #b9b9b9;
    h3 { padding: 25px 0;}
    .artist-unfollow-list {
      margin: 15px 0;
      padding-bottom: 15px;
      border-bottom: 1px solid #dadada;
      .artist-img {
        margin-right: 15px;
        height: 72px;
        width: 72px;
        border-radius: 50%;
        background-size: cover !important;
        background-repeat: no-repeat !important;
        background-position: center center !important;
      }
      h6 { padding-top: 5px; color: #b9b9b9; font-weight: $font_weight_500;}
      .btn-outline {
        padding: 10px 25px 8px;
        border-color: #dadada;
        color: $color_light_black;
        font-size: 12px;
        line-height: 16px;
        font-weight: $font_weight_600;
      }
    }
  }

  /* Address List Css */
  .address-blk-list {
    margin-bottom: 15px;
    padding: 15px;
    border: 1px solid #DADADA;
    border-radius: 10px;
    h6 { font-weight: $font_weight_600;}
    .address-tag {
      margin: 0px 15px;
      display: inline-block;
    }
    .address-info {
      padding: 15px 0 0;
    }
    &.btn-outline { 
      width: 100%; 
      color: $color_blue;
      img {
          object-fit: none; object-position: 0 0px; width: 20px; height: 20px;
        }
      &:hover img,&:focus img {
        object-position: 0 -20px;
      } 
    }
  }
  .edit-dlt-ic {
    padding: 10px 0;
    font-size: 14px;
    font-weight: $font_weight_700;
    color: $color_blue !important;
    &:hover { text-decoration: underline;}
    &:last-child { color: #FF0000 !important;}
    img {
      margin-right: 10px;
      vertical-align: sub;        
    }
  }
  .add-address-sec {
    .add-new-btn, .card {
      border: 1px solid #dadada;
      box-shadow: none;
    }
    .add-new-btn:hover { border-color: $color_blue;}
  }

  /* Reviews Css */
  .reviews-blk-list {
    .prod-img {
      margin-right: 15px;
      height: 100px;
      width: 100px;
      min-width: 100px;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      background-position: center center !important;
    }
    .review-text {
      h5,p {
        padding-top: 10px; 
        font-size: 12px;
        line-height: 19px;
        font-weight: $font_weight_500;
      }
      h5 { color: $color_grey;}
    }
    .total-review-digit { 
      img { margin-right: 5px;}
      span { vertical-align: middle;}
    }
    .edit-dlt-ic { 
      padding: 0 35px 0 0;
    }
  }

  /* Notification Css */
  .notification-blk-list {
    p { margin-bottom: 0;}
    .notification-icons {
      margin-right: 15px;
      img { height: 80px; width: 80px; object-fit: cover;}
    }
  }

  /* Tokens Css */
  .token-blk {
    .token-content {
      padding-bottom: 60px;
      background: url('../../../images/token-bg.svg') no-repeat right bottom / contain;
      h3 { padding: 0;}
      .card-text {
        width: 417px;
      }
      .token-balance {
        position: relative;
        h6 { font-weight: $font_weight_500;}
        h2 { margin: 15px 0 0; color: $color_blue;}
        margin: 20px 0;
        padding: 15px;
        border: 2px dashed #dadada;
        border-radius: 12px;
        img { position: absolute; right: 0; bottom: 0;}
      }
    }
    ul {
      li { 
        padding: 3px 0 3px 12px; 
        font-size: 10px; 
        line-height: 19px;
        text-transform: none;
        color: $color_light_black;
        position: relative;
        &::before { content: ''; background: #000000; height: 5px; width: 5px; top: 10px; left: 0; position: absolute; display: block; border-radius: 50%;}
      }
    }
    .token-table {
      padding: 0;
      margin-top: 25px;
      h3 { padding: 15px;}
      table {
        margin: 15px 0;
        width: 100%;
        th,td { padding: 12px 15px; border: 1px solid #f4f4f4; color: $color_light_black;}
        th,td { 
          &:last-child { text-align: center;}
        }
        th    { font-size: 14px; line-height: 19px; text-transform: uppercase; letter-spacing: 1px;}
        td    { text-transform: capitalize;}
        .dabit-token { color: $color_red; font-weight: $font_weight_600;}
        .credit-token { color: $color_green; font-weight: $font_weight_600;}
      }
    }
  }

  /* Sub Users*/
  .table-borderless.subusers-table{
    th{
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 1px;
      color: $color_light_grey;
      text-transform: uppercase;
      font-weight: $font_weight_700;
    }
    tr.clickable-row{
      cursor: pointer;
      td{
        padding: 12px;
        font-size: 16px;
        line-height: 19px;
        font-weight: $font_weight_500;
        color: $color_light-black;
        border-bottom: 1px solid #dadada;
      }
      &:hover{
        td{
          color: $color_blue;
        }
      }
    }
  }

  /* Responsive Css */
  @media ( min-width: 992px) and ( max-width: 1199px) {
    /* Account Links Css */
    .account-links-blk {
      .account-item-list {
        .card {
          .card-body {
            li { 
              a { font-size: 14px; line-height: 19px;}
            }
          }
        }
      }
    }
    /* Profile Information Css */
    .user-profile-details {
      .user-img {
        height: 100px;
        width: 100px;
        .box label {
          bottom: -8px;
        }
      }
    }
    /* Address List Css */
    .edit-dlt-ic {
      font-size: 12px;
    }
    /* Reviews Css */
    .reviews-blk-list {
      .align-center { align-items: flex-start; -webkit-align-items: flex-start;} 
      .prod-img {
        height: 80px;
        width: 80px;
        min-width: 80px;
      }
    }
    /* Tokens Css */
    .token-blk {
      .token-table {
        table {
          margin-top: 0;
          th,td { 
            padding: 10px;
            font-size: 13px;
            line-height: 20px;
          }
        }
      }
    }
  } 

  @media ( min-width: 768px) and ( max-width: 991px) {
    .card                         { padding: 15px;}
    h2                            { margin-bottom: 15px;}
    .form-field {
      .btn {
        margin: 10px 5px 5px; min-width: 140px;
        &.btn-outline {  padding: 10px 25px;}
      }
    }
    /* Account Links Css */
    .account-links-blk {
      margin-bottom: 25px;
      .account-item-list {
        .card {
          .card-body {
            li { 
              margin: 0; padding: 10px 0 5px; width: calc( 100% / 3); display: inline-block;
              a { font-size: 14px; line-height: 19px;}
            }
          }
        }
      }
    }
    /* Profile Information Css */
    .user-profile-details {
      .user-img {
        height: 100px;
        width: 100px;
        .box label {
          bottom: -8px;
        }
      }
    }
    .unfollowing-artist {
      h3                            { padding: 15px 0;}  
      .artist-unfollow-list {
        h4                          { font-size: 16px; line-height: 19px;}
        .artist-img {
          height: 65px;
          width: 65px;
        }
      }
    }
    /* Address List Css */
    .edit-dlt-ic {
      margin: 10px 15px 0px 0;
    }
    /* Reviews Css */
    .reviews-blk-list {
      .align-center { align-items: flex-start; -webkit-align-items: flex-start;} 
      .prod-img {
        height: 70px;
        width: 70px;
        min-width: 70px;
      }
      .edit-dlt-ic  { padding: 0;}
      p { margin: 0;}
    }
    /* Notification Css */
    .notification-blk-list {
      .notification-icons img { 
        height: 70px;
        width: 70px;
      }
    } 
    /* Tokens Css */
    .token-blk {
      .token-table {
        table {
          margin-top: 0;
          th,td { 
            padding: 10px;
            font-size: 13px;
            line-height: 20px;
          }
        }
      }
    }
  } 

  @media ( max-width: 767px) {
    .card                         { padding: 15px;}
    h2                            { margin-bottom: 15px;}
    .form-field {
      .btn {
        margin: 10px 5px 5px; min-width: 120px;
        &.btn-outline {  padding: 10px 25px;}
      }
    }
    /* Account Links Css */
    .account-links-blk {
      margin-bottom: 25px;
      .account-item-list {
        .card {
          .card-body {
            li { 
              margin: 0; padding: 8px 0 5px;
              a { font-size: 14px; line-height: 19px;}
            }
          }
        }
      }
    }
    .my-account-content h3 { padding-bottom: 15px; text-align: center;}
    /* Profile Information Css */
    .user-profile-details {
      text-align: center;
      h3                            { padding: 0;}
      .align-center                 { display: block;}
      .user-img {
        height: 80px;
        width: 80px;
        margin: 0px auto 15px;
        img                         { width: 25px;}
        .box label {
          bottom: -8px;
        }
      }
    }
    .unfollowing-artist {
      h3                            { padding: 15px 0;}  
      .align-center                 { display: block;}
      .artist-unfollow-list {
        h4                          { font-size: 13px; line-height: 19px;}
        h6                          { font-size: 12px; line-height: 16px;}
        .artist-img {
          height: 50px;
          width: 50px;
        }
        .btn-outline {
          padding: 10px;
          margin: 10px auto 0;
          display: block;
        }
      }
    }
    /* Address List Css */
    .address-blk-list {
      padding: 12px 15px;
      text-align: center;
      .align-center                                   { display: block;}
      .address-tag {
        margin: 10px 0;
      }
      .address-contact {
        a                                             { font-size: 13px; line-height: 16px;}
      }
      .address-info {
        h5                                            { padding-bottom: 10px;}
        p.small-font                                  { margin: 0 0 10px;}
        .col-md-6 { 
          margin-bottom: 10px; border: none; border-bottom: 1px solid #d8d8d8;
          &:last-child                                { border: none;}                              
        }
      }
    }
    .edit-dlt-ic {
      width: calc(100% / 2);
      img                                             { vertical-align: sub;}
    }
    /* Reviews Css */
    .reviews-blk-list { 
      .prod-img {
        margin: 0 auto 15px;
        height: 70px;
        width: 70px;
        min-width: 70px;
      }
      .edit-dlt-ic  { padding: 0;}
      .total-review-digit { display: block;}
    }
    /* Notification Css */
    .notification-blk-list {
      .notification-icons img { 
        margin-bottom: 10px;
        height: 60px;
        width: 60px;
      }
    } 
    .notification-btn { font-size: 10px;}
    /* Tokens Css */
    .token-blk {
      .token-content {
        .card-text {
          width: 100%;
        }
        .token-balance {
          padding: 10px;
          img { width: 45px;}
        }
      }
      .token-table {
        table {
          margin-top: 0;
          th,td { 
            padding: 10px;
            font-size: 11px;
            line-height: 16px;
          }
          th { min-width: 100px;}
        }
      }
    }
  }
}