.projects-table {
  .btn-1 {
    background: $color_light-black;
    &:hover { background: $color_blue;}
    i { padding-right: 10px;}
  }
  .btn-outline { margin-left: 0; margin-right: 15px;}
  .project-img {
    margin-right: 15px;
    height: 70px;
    width: 70px;
    background-size: contain !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
  }
  .table-style h5 { font-weight: $font_weight_600;}
  .select-project-popup {
    display: none;
    padding: 8px 25px;
    position: fixed;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    width: 900px;
    background: $color_red;
    border: 2px solid $color_white;
    border-radius: 28px;
    box-shadow: 0px 4px 16px rgba(0,0,0,0.25);
    z-index: 100;
    h5 { color: $color_white; font-weight: $font_weight_700;}
    .btn { 
      margin-left: 25px; padding: 0; background: transparent; border: none;
      img { width: auto;}
    }
    .border-img { 
      padding: 5px; height: 35px; width: 35px; border-radius: 50%; border: 1px solid $color_white;
      &:hover { background: $color_light-black; border-color: $color_light-black;}
    }
    .btn-1 {
      padding: 8px 25px;
      background: $color_white;
      color: $color_red;
      letter-spacing: 1px;
      &:hover { background: $color_light-black; color: $color_white;}
    }
  }
}
/* Activate Tab Css */
.activate-tab {
  margin: 0 0 20px 44px !important;
  .nav-tabs {
    padding: 5px;
    background: #f4f4f4;
    border: none;
    border-radius: 24px;
    .nav-item {
      .nav-link {
        margin-bottom: 0;
        padding: 5px 10px;
        border: none;
        border-radius: 19px;
        font-size: 14px;
        line-height: 22px;
        font-weight: $font_weight_600;
        color: $color_grey !important;
        width: 130px;
        transition-duration: .3s;
        text-align: center;
        cursor: pointer;
        &.active {
          background-color: $color-green;
          color: $color_white !important;
        }
      }
      &.deactivate {
        .nav-link.active { background: $color_red;}
      }
    }
  }
}
/* Action Css */
.action-style {
  .popover {
    margin-top: 10px;
    right: 0;
    border: none;
    max-width: 130px;
    box-shadow: none;
    background: transparent;
    font-family: 'Montserrat', sans-serif;
    .popover-inner {
      padding: 5px 15px;
      background-color: $color_white;
      box-shadow: 0px 2px 16px rgba(0,0,0,0.18);
      border-radius: 8px;
      .btn {
        margin: 10px 0;
        padding: 0;
        background: transparent !important;
        border: none;
        color: $color_light-black;
        font-size: 14px;
        line-height: 19px;
        font-weight: $font_weight_600;
        display: block;
        letter-spacing: 1px;
        &:hover, &:focus { color: $color_red;}
      }
    }
  }
  .bs-popover-auto[x-placement^=bottom]>.arrow::before, 
  .bs-popover-bottom>.arrow::before  { border-bottom-color: transparent;}
}
/* Action Css */
.action-modal {
  .btn {
    padding: 0;
    background: transparent;
  }
  img { 
    cursor: pointer; object-fit: none; object-position: 0 0px; width: 6px; height: 22px;
    &:hover { object-position: 0 -22px;}
  }
}
/*Delete Msg Modal*/
.delete-modal{
  .modal-body {
    padding: 35px 22px 15px !important;
  }
  .btn {
    margin: 0px 10px;
    width: 160px !important;
    &.btn-1 {
      background: $color_red;
      &:hover { background: $color_light-black;}
    }
    &.btn-outline {
      border-color: $color_red;
      color: $color_red;
      &:hover { background: $color_light-black; border-color: $color_light-black !important;}
    }
  }
}