.artist-profile-setup{
  .login-right {
    h2{ text-align: left;}
    p { text-align: right;}
  }
  .login-blk { width: 100% !important;}
  h5 {
    padding: 10px 0 35px;
  }
  h6 {
    padding: 15px 0;
    color: #dadada;
  }
  .artist-img {
    margin-right: 15px;
    height: 80px;
    width: 80px;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    background-position: center center !important;
    border-radius: 50%;
    position: relative;
    .box {
      position: absolute;
      bottom: -12px;
      right: 0;
      img { width: 30px;}
      label { cursor: pointer;}
    }
  }
  .btn-outline {
    width: 100%;
  }
  @media (min-width: 1200px) and (max-width: 1499px) { 
    h5 {
      padding-bottom: 20px;
    }
  }
}
.input-group {
  .input-group-text {
    padding: 0;
    border: none;
    background: transparent;
    position: absolute;
    left: 10px;
    top: 10px;
    z-index: 10;
    img { width: 25px;}
  }
  .form-control { padding-left: 45px;}
}
