.artist-header{
  position: fixed; top: 0; left: 0; width: 100%; z-index: 10;
  padding: 5px 15px; background: #fff; box-shadow: inset 0px -1px 0 0 rgba(198, 198, 198, 0.50);
  .navbar { padding: 0;}
  .nav-item {
    padding: 0px 15px;
    display: inline-block;
    &.active {
      .nav-link{ 
        color: $color_blue;
        img {object-position: 0 -20px; }
      }
    }
    .nav-link {
      padding: 0; font-weight: 500; font-size: 16px; line-height: 19px; color: #464546; text-transform: capitalize;
      img { margin-right: 12px; object-fit: none; object-position: 0 0px; width: 20px; height: 20px;}
      &:hover img { object-position: 0 -20px;}
      &.paymet-ic {
        img { height: 16px;}
        &:hover img { object-position: 0 -16px;}
      }
    }
  }
  .artist-header-right {
    .nav-link {
      img { margin-right: 0;}
    }
    .artist-account {
      .artist-img {
        margin-right: 10px;
        height: 48px;
        width: 48px;
        border-radius: 50%;
        background-size: cover !important;
        background-position: center center !important;
        background-repeat: no-repeat !important;
      }
      h6 {
         font-weight: $font_weight_600;
         i { padding-left: 5px;}
        }
      p { margin: 0; color: $color_grey; font-weight: $font_weight_600;}
    }
  }
  @media (min-width: 1200px) and (max-width: 1499px) { 
    .nav-item {
      padding: 0px 10px;
      .nav-link {
        font-size: 14px;
      }
    }
  }
}