.header-content {
  &.mobile-menu { display: none;}
  position: fixed; top: 0; left: 0; width: 100%; z-index: 1040;
  padding: 5px 15px; background: #fff; box-shadow: inset 0px -1px 0 0 rgba(198, 198, 198, 0.50);
  .navbar { padding: 0;}
  .nav{
    align-items: center;
  }
  .nav-item { 
    padding: 8px 15px; display: inline-block;
    .nav-link { 
      padding: 0px 0; font-weight: 500; font-size: 16px; line-height: 19px; color: #464546; text-transform: capitalize;
      &:hover{ color: $color_blue;}
    }
    &.sale-link .nav-link:hover {
      color: $color_red !important;
    }
  }
  .category-dropdown {
    // .nav-link:first-child { padding: 0;}
    .dropdown.nav-item {
      padding: 8px 0;
      // .nav-link { padding: 8px 0;}
      // &:hover{
      //   .dropdown-menu { display: block;}
      // }
      .dropdown-menu{
        padding: 0; cursor: default; background: #ffffff; border: none; top: 35px; left: 0px; min-height: 250px; min-width: 400px; box-shadow: 0px 2px 16px 0 rgba(0,0,0,0.13); border-radius: 8px;
        .dropdown-left-menu{
          padding: 15px 0; background: #f9f9f9; width: 170px; min-height: 250px; border-radius: 8px 0 0 8px;
          .dropdown-item {
              padding: 10px 25px; font-size: 14px; line-height: 18px; font-weight: $font_weight_600; color: #464546; width: auto; position: relative;
              &:hover, &:active{ 
                  color: $color_blue; background: transparent;
                  &::before{ content: ''; background: $color_blue; height: 16px; width: 8px; position: absolute; top: 50%; left: 0; transform: translateY(-50%); -webkit-transform: translateY(-50%);}
              }
          }
        }
        &::before { top: -20px; left: 10px; border: solid transparent; content: " "; height: 0; width: 0; position: absolute; pointer-events: none; border-bottom-color: #F9F9F9; border-width: 10px;}
        .dropdown-submenu { 
          padding: 15px 25px; width: calc(100% - 170px); 
          li{
            padding: 10px 0;
            .nav-link { font-size: 14px; line-height: 18px; font-weight: $font_weight_600; color: #464546; display: inline-block;
              &:hover { color: $color_blue;}
            }
          }
        }
        &.youth-menu {
          min-width: 550px;
          .dropdown-submenu {
            padding: 25px;
            h5 { padding-bottom: 5px; font-weight: $font_weight_600;}
            li {
              padding: 5px 0;
              .nav-link { font-weight: $font_weight_300;}
            }
            .mega-menu-level-3 {
              margin-bottom: 20px;
              display: inline-block;
              width: calc(100% / 2);
              vertical-align: top;
            }
          }
        }
      }
    }
  }
    .menu-right{
      .search-ic {
        img { 
          object-fit: none; object-position: 0 0px; width: 24px; height: 24px;
        }
        &:hover img {
            object-position: 0 -24px;
        }
      }
      .whishlist-ic {
        img { 
          object-fit: none; object-position: 0 0px; width: 20px; height: 18px;
        }
        &:hover img {
            object-position: 0 -18px;
        }
      }
      .cart-ic {
        position: relative;
        img { 
          object-fit: none; object-position: 0 0px; width: 24px; height: 22px;
        }
        &:hover img {
            object-position: 0 -22px;
        }
       span {
        padding: 1px 4px;
        height: 20px;
        width: 20px;
        border-radius: 50%;
        background: $color_red;
        font-size: 12px;
        color: $color_white;
        position: absolute;
        top: -10px;
        right: -10px;
        display: inline-block;
        }
      }
      .category-dropdown.sample-dropdown {
        .dropdown.nav-item {
          // .nav-link { padding: 15px 0;}
          .logout-text { color: $color_red;}
          .dropdown-menu {
            left: auto;
            right: 0;
            top: 40px;
            min-width: 100%;
            min-height: 100%;
            .dropdown-left-menu { 
                padding: 10px 0;
                background: #fff;
                min-height: 100%;
                .dropdown-item { width: 100%;}
            }
            &::before { border-bottom-color: #fff; right: 5px; left: auto;}
          }
        }
      }
      .customize-btn { 
        padding: 10px 25px; font-size: 14px; line-height: 18px; background: $color_blue; color: $color_white !important; border-radius: 18px; text-transform: uppercase; font-weight: 700;
        &:hover { background: $color_light-black;}
      }
    }
  /*Responsive Design */
  @media ( min-width: 768px) and (max-width: 991px) { 
    &.mobile-hide { display: none;}
    &.mobile-menu { display: block;}
    padding: 0;
    box-shadow: none;
    .navbar {
      padding: 10px;
      background: $color_blue;
      .navbar-brand img { width: 45px;}
      .menu-right {
        text-align: right;
        width: 100%;
        > .nav-item { padding: 10px;}
        .user-ic img, .cart-ic img { 
          object-fit: contain;
          height: 100%;
          object-position: 0 0 !important;
        }
        .cart-ic span {
          padding: 0px 4px;
          border: 1px solid $color_white;
          font-size: 11px;
        }
        .category-dropdown.sample-dropdown {
          .dropdown.nav-item .dropdown-menu {
            .dropdown-item { padding: 7px 15px;}
            &::before { right: 0;}
            .dropdown-left-menu { width: 150px;}
          }
        } 
      }
    }
    .mobile-search {
      display: block;
      box-shadow: inset 0px -1px 0 0 rgba(198, 198, 198, 0.50);
      .form-group {
        margin: 0;
        position: relative;
        &::before { z-index: -1; content: ''; background: url('../../images/search-ic.svg')no-repeat; height: 100%; width: 100%; position: absolute; top: 14px; left: 12px;}
        .form-control {
          padding: 15px 15px 15px 45px;
          background: none;
          border: none;
          &::placeholder{
            color: $color_grey;
        }
          &::-moz-placeholder{
              color: $color_grey;
          }
          &::-webkit-input-placeholder{
              color: $color_grey;
          }
          &:-ms-input-placeholder{
              color: $color_grey;
          }
          &::-o-placeholder{
              color: $color_grey;
          }
        }
      }
    }
  }
  @media ( max-width:767px){
    &.mobile-hide { display: none;}
    &.mobile-menu { display: block;}
    padding: 0;
    box-shadow: none;
    .navbar {
      padding: 10px;
      background: $color_blue;
      .navbar-brand img { width: 45px;}
      .menu-right {
        > .nav-item { padding: 10px;}
        .user-ic img, .cart-ic img { 
          object-fit: contain;
          height: 100%;
          object-position: 0 0 !important;
        }
        .cart-ic span {
          padding: 0px 4px;
          border: 1px solid $color_white;
          font-size: 11px;
        }
        .category-dropdown.sample-dropdown {
          .dropdown.nav-item .dropdown-menu {
            .dropdown-item { padding: 7px 15px;}
            &::before { right: 0;}
            .dropdown-left-menu { width: 150px;}
          }
        } 
      }
    }
    .mobile-search {
      display: block;
      box-shadow: inset 0px -1px 0 0 rgba(198, 198, 198, 0.50);
      .form-group {
        margin: 0;
        position: relative;
        &::before { z-index: -1; content: ''; background: url('../../images/search-ic.svg')no-repeat; height: 100%; width: 100%; position: absolute; top: 11px; left: 12px;}
        .form-control {
          padding: 12px 15px 12px 45px;
          background: none;
          border: none;
          &::placeholder{
            color: $color_grey;
        }
          &::-moz-placeholder{
              color: $color_grey;
          }
          &::-webkit-input-placeholder{
              color: $color_grey;
          }
          &:-ms-input-placeholder{
              color: $color_grey;
          }
          &::-o-placeholder{
              color: $color_grey;
          }
        }
      }
    }
  }
}
/* Search Modal */
.modal{
    .search-modal {
      max-width: 1000px;
      .modal-content {
        &::before { content: ''; background: #464546; height: 6px; width: 65px; position: absolute; top: 0; left: 50%; transform: translateX(-50%); -webkit-transform: translateX(-50%); display: block; border-radius: 0px 0px 100px 100px;} 
        border-radius: 16px;
        .modal-header { 
          padding: 10px 15px;
          border: none;
          border-bottom: 1px solid #DADADA;
          position: relative;
          .modal-title { 
              width: 100%;
          }
          .close {
              margin: 5px;
              padding: 0;
              opacity: 1;
              span { 
                  background: #FF654F; color: $color_white; height: 24px; width: 24px; display: block; border-radius: 50px;
                  &:hover { background: $color_light-black;}
              }
          } 
          .clear-btn { 
              padding: 0; font-size: 12px; line-height: 16px; font-weight: $font_weight_700; color: $color_grey; text-transform: uppercase; letter-spacing: 1px; position: absolute; right: 65px; top: 20px;
          }
          .search-blk {
              margin: 0;
              position: relative;
              &::before { content: ''; background: url('../../images/search-ic.svg') no-repeat; height: 24px; width: 24px; position: absolute; top: 5px; left: 0;}
              input { 
                padding: 5px 40px; border: none; width: 95%; font-size: 16px; line-height: 19px;
                &:focus { outline: none;}
                &::placeholder{
                    color: #878687;
                    font-weight: $font_weight_500;
                }
                &::-moz-placeholder{
                    color: #878687;
                    font-weight: $font_weight_500;
                }
                &::-webkit-input-placeholder{
                    color: #878687;
                    font-weight: $font_weight_500;
                }
                &:-ms-input-placeholder{
                    color: #878687;
                    font-weight: $font_weight_500;
                }
                &::-o-placeholder{
                    color: #878687;
                    font-weight: $font_weight_500;
                }
              }
            }
          }
          .modal-body{
            padding: 28px 20px;
            h4 { padding-bottom: 15px; color: $color_light-black;}
            .search-product-listing {
              .search-product-blk {
                width: calc( 100% / 3 - 10px);
                margin: 7px;
                padding: 10px;
                border: 1px solid #F4F4F4;
                border-radius: 4px;
                display: inline-block;
                &:nth-child(3n+1) { margin-left: 0;}
                &:nth-child(3n+3) { margin-right: 0;}
                .search-product-img {
                  margin-right: 20px;
                  img { 
                      height: 80px;
                      width: 80px;
                      object-fit: cover;
                  }
                }
                .search-product-details {
                  p.small-font { margin-bottom: 5px; color: $color_grey;}
                  a { 
                      line-height: 19px; font-weight: $font_weight_500; color: $color_light-black;
                      display: -webkit-box;
                      -webkit-line-clamp: 2;
                      -webkit-box-orient: vertical;  
                      overflow: hidden;
                  }
                }
              }
            }
            .after-search-product {
              h4 { 
                font-size: 16px; line-height: 22px;
                a { padding-left: 15px; font-size: 12px; line-height: 16px; color: #60CED9; text-transform: uppercase; letter-spacing: 1px; font-weight: $font_weight_700;}
              }
              .search-product-listing 
              {
            .search-product-blk {
                width: 100%;
                margin: 10px 0;
            }
          }
        }
      }
    }
  }
}