.chat-sec {
  .full-height {
    height: calc(100vh - 100px); max-height: 950px;
  }
  h6 { font-size: 14px; line-height: 19px; font-weight: $font_weight_700;}
  p { margin-bottom: 0; font-size: 14px; line-height: 19px;}
  .chat-user-list {
    padding: 15px 0;
    width: 470px;
    height: 100%;
    flex-basis: 470px;
    -webkit-flex-basis: 470px;
    border-right: 1px solid #dadada;
    h5 { padding: 0px 15px 15px; text-transform: uppercase;}
    ul {
      li {
        margin-right: 10px;
        padding: 10px 10px 10px 15px;
        position: relative;
        &::before { content: ''; background: #f3f3f3; height: 1px; width: 361px; position: absolute; bottom: 0; right: 15px;}
        &:hover { 
          background: $color_blue;
          h6,p,h4 { color: $color_white !important;}
          cursor: pointer;
          &::before { display: none;}
        }
        .tee-prod-img {
          padding: 8px;
          margin-right: 15px;
          height: 48px;
          width: 48px;
          background: #f4f4f4;
          border-radius: 50%;
          object-fit: cover;
        }
        .active-chat-blk {
          width: 100%;
          h4 { margin-left: 10px; padding-bottom: 5px; font-size: 12px; line-height: 16px; color: $color_grey; font-weight: $font_weight_300;}
          span { height: 12px; width: 12px; background: $color_blue; border-radius: 50%; display: block; float: right;}
        }
      }
    }
    .rcs-custom-scroll {
      .panel-content-custom {
        padding: 0;
        max-height: calc(100vh - 170px);
        width: 100%;
      }
    }
  }
  .chat-window {
    width: calc(100% - 470px);
    flex-basis: calc(100% - 470px);
    -webkit-flex-basis: calc(100% - 470px);
    .project-title {
      padding: 10px 35px 10px 15px;
      background: $color_white;
      box-shadow: 0px 4px 11px rgba(0,0,0,0.04);
      .tee-prod-img {
        padding: 6px;
        margin-right: 15px;
        height: 40px;
        width: 40px;
        background: #f4f4f4;
        border-radius: 50%;
        object-fit: cover;
      }
    }
    h3 { padding: 15px 0; text-align: center; font-size: 14px; line-height: 19px; color: #B9B9B9; text-transform: uppercase; letter-spacing: 1px;}
    .chat-day-msg {
      padding: 15px 20px;
      .receiver-msg {
        h5 { max-width: 450px; padding: 10px; text-transform: capitalize; border: 1px solid #b9b9b9; display: inline-block; border-radius: 12px 12px 12px 0;}
        .chat-time {
          padding-top: 5px;
          display: block;
          font-size: 12px;
          line-height: 16px;
          color: $color_grey;
          font-weight: $font_weight_500;
        }
        &.sender-msg {
          text-align: right;
          h5 { background: $color_light_black; border: none; color: $color_white; border-radius: 12px 12px 0 12px;}
          .chat-img { border-radius: 12px 12px 0 12px;}
        }
        .chat-img {
          padding: 10px;
          border: 1px solid #b9b9b9;
          border-radius: 12px 12px 12px 0;
          display: inline-block;
          img {
            height: 130px;
            width: 130px;
            object-fit: cover;
          }
        }
      }
    }
    .rcs-custom-scroll {
      .panel-content-custom {
        padding: 0;
        max-height: calc(100vh - 220px);
        width: 100%;
      }
    }
    .type-send-msg {
      padding: 10px 15px;
      background: #f9f9f9;
      .form-control {
        padding: 0 12px;
        background: transparent;
        border-radius: 0;
        border: none;
        width: 100%;
      }
      .box {
        padding: 0px 10px 0 0;
        border-right: 1px solid #d9d9d9;
        label {
          margin: 0;
          cursor: pointer;
          img { object-fit: none; object-position: 0 0px; width: 20px; height: 22px;}
          &:hover img { object-position: 0 -22px;}
        }
      }
      .btn { 
        padding: 8px 12px 10px 10px; height: 40px; width: 40px; border-radius: 50%; background: $color_red; border: none;
        &:hover { background: $color_light_black;}
      }
    }
  }
}
/*Delete Msg Modal*/