.artist-details {
  border-bottom: 1px solid #dadada;
  .artist-info {
    padding-bottom: 35px;
    .artist-img {
      margin-right: 25px;
      background-size: cover !important; 
      background-position: center center !important;
      background-repeat: no-repeat !important;
      width: 120px; 
      height: 120px;
      border-radius: 50%;
    }
    .artist-social {
      padding-top: 10px;
      li {
        padding-right: 15px;
        display: inline-block;
        a {
          font-size: 24px;
          line-height: 28px;
          color: #DADADA;
        }
      }
    }
    .btn-outline {
      padding: 8px 20px;
      border-color: $color_blue;
      color: $color_blue;
    }
  }
  .artist-brief-info {
    padding-bottom: 35px;
    h6 { color: #dadada;}
    p { margin: 10px 0; line-height: 25px; font-weight: $font_weight_500;}
    a { color: $color_blue;}
  }

  /*Responsive*/
  @media (min-width: 1200px) and (max-width: 1499px) { 
    .artist-brief-info {
      padding-bottom: 30px;
    }
  }

  @media ( min-width: 768px) and ( max-width: 991px) { 
    .artist-info {
      .artist-img {
        height: 80px;
        width: 80px;
      }
      h2            { font-size: 18px; line-height: 22px;}
      .artist-social { padding: 10px 0;}
    }
  }

  @media ( max-width: 767px) {
    .artist-info {
      display: block;
      text-align: center;
      .align-center { display: block;}
      .artist-img {
        height: 80px;
        width: 80px;
        margin: 0 auto 15px;
      }
      h2            { font-size: 16px; line-height: 22px;}
      .artist-social { padding: 10px 0;}
    }
    .artist-brief-info {
      padding-bottom: 15px;
    }
  }
}
.my-design-sec {
  padding-bottom: 70px;
  .products-blk {
    .artist-nm { display: none;}
  }
  @media ( min-width: 768px) and ( max-width: 991px) {
    padding-bottom: 35px;
    .view-all-btn { margin-top: 15px;}
  }
  @media ( max-width: 767px) {
    padding-bottom: 35px;
    .view-all-btn { margin-top: 15px;}
  }
}