.notifications-sec {
  background: #f9f9f9;
  .notification-centent {
    width: 864px;
    margin: 0px auto;
    .notification-blks {
      margin: 15px 0;
      .card { 
        margin: 18px 0; padding: 15px; box-shadow: none;
        .notification-ic {
          margin-right: 15px;
          padding: 13px;
          height: 48px;
          width: 48px;
          border-radius: 50%;
          background: #f4f4f4;
        }
        h6 span { font-weight: $font_weight_300;}
        h5 { padding-top: 5px; color: $color_grey;}
      }
    }
  }
}